/** @module Actions:AssetMap */
/**
 * Created by jimsugg on 4/23/17.
 */

import { newBsDmId } from '../utils';

import { BsAssetItem, BsAssetId, bscGenerateAssetLocator } from '@brightsign/bscore';
import { DmState, dmGetAssetIdByLocator } from '../reducers/index';
import { BsDmAction, BsDmThunkAction, dmFilterDmState } from './actionBase';
import { DmErrorType, BsDmError } from '../bsDmError';

// Actions
/** @internal */
/** @private */
export const PUT_ASSET_ITEM = 'DM_PUT_ASSET_ITEM';

/** @internal */
/** @private */
export const REMOVE_ASSET_ITEMS = 'DM_REMOVE_ASSET_ITEMS';

export type AssetItemParams = BsAssetItem;
export type AssetItemAction = BsDmAction<AssetItemParams>;

export function dmPutAssetItem(params: AssetItemParams): BsDmThunkAction<AssetItemParams> {
  return (dispatch, getState): AssetItemAction => {
    const putAction: AssetItemAction = {
      type: PUT_ASSET_ITEM,
      payload: Object.assign({}, params),
    };
    const locator = bscGenerateAssetLocator(params);
    if (!locator) {
      // If locator isn't valid, the assetItem isn't valid
      throw new BsDmError(DmErrorType.invalidParameters,
        'Asset Item does not contain valid locator information', putAction);
    }

    // Save generated locator with payload
    putAction.payload.locator = locator;
    const dmState = dmFilterDmState(getState());
    if (!putAction.payload.id) {
      // If there is no ID, check to see if we already have an asset with the same locator
      // Set ID to matching ID (if found,) or newly generated ID
      dmSetAssetId(dmState, putAction.payload);
    }
    dispatch(putAction);
    return putAction;
  };
}

/** @internal */
/** @private */
/**
 * Look for AssetItem locator in current AssetMap. If found, set the assetId for the AssetItem to the matching ID.
 * If not found, set the assetId to a newly generated ID.
 * This function will also regenerate the locator and set the generated value to the AssetItem.
 *
 * @param state {DmState} - state object in which to lookup asset locator in asset map
 * @param assetItem {BsAssetItem} - asset item to set with matching ID, if found
 * @returns {BsAssetItem}
 */
export function dmSetAssetId(state: DmState, assetItem: BsAssetItem): BsAssetItem {
  const locator = bscGenerateAssetLocator(assetItem);
  const assetId = dmGetAssetIdByLocator(state, {locator});
  assetItem.id = assetId ? assetId : newBsDmId();
  assetItem.locator = locator;
  return assetItem;
}

// Used internally to put assetItem without using a thunk to test locator info
/** @internal */
/** @private */
export function dmPutAssetItemWithoutTest(params: AssetItemParams): AssetItemAction {
  const putAction: AssetItemAction = {
    type: PUT_ASSET_ITEM,
    payload: Object.assign({}, params),
  };
  const locator = bscGenerateAssetLocator(params);
  // Save generated locator with payload
  putAction.payload.locator = locator;
  if (!putAction.payload.id) {
    putAction.payload.id = newBsDmId();
  }
  return putAction;
}

/** @internal */
/** @private */
export interface AssetItemRemoveParams {
  assetIds: BsAssetId[];
}
/** @internal */
/** @private */
export type AssetItemRemoveAction = BsDmAction<AssetItemRemoveParams>;

/** @internal */
/** @private */
export function dmRemoveAssetItems(id: BsAssetId | BsAssetId[]): AssetItemRemoveAction {
  const assetIds = typeof id === 'string' ? [id] : id;
  return {
    type: REMOVE_ASSET_ITEMS,
    payload: {assetIds},
  };
}



