export interface DmMap<T extends DmObject> {
  [id: string]: T;    // really '[id:BsDmId]: T;' -- but Typescript doesn't like that, even though BsDmId = string
}

// Support for batched actions, as done in the redux-batched-actions package
export type DmReducer = Reducer<DmState>;
function enableBatching(reduce): DmReducer {
  return function batchingReducer(state: DmState, action: BsDmBaseAction | BsDmBatchAction): DmState {
    switch (action.type) {
      case BSDM_BATCH:
        return (action as BsDmBatchAction).payload.reduce(batchingReducer, state);
      default:
        return reduce(state, action);
    }
  };
}

/**
 * DmState is the Redux state shape for bsDataModel. It can be included in a larger Redux store. If so, it must have
 *  a property name of 'bsdm' or 'bsdatamodel'. This will enable the complete containing state to be passed to
 *  selectors and thunked actions
 * The internal properties of DmState are considered opaque and external users should not rely on any assumptions
 *  about its internal structure. The internal structure is subject to change at any time.
 * When creating the Redux store, you must apply the redux-thunk middleware
 * @interface DmState
 */
export interface DmState {
  sign: DmSignMetadata;
  zones: DmZoneCollectionState;
  mediaStates: DmMediaStateCollectionState;
  events: DmEventCollectionState;
  transitions: DmTransitionCollectionState;
  htmlSites: DmHtmlSiteCollectionState;
  dataFeeds: DmDataFeedCollectionState;
  userVariables: DmUserVariableCollectionState;
  assetMap: DmAssetMapState;
}

export const bsDmReducer = enableBatching(combineReducers<DmState>({
  sign: signReducer,
  zones: zoneReducer,
  mediaStates: mediaStateReducer,
  events: eventReducer,
  transitions: transitionReducer,
  htmlSites: htmlSiteReducer,
  dataFeeds: dataFeedReducer,
  userVariables: userVariableReducer,
  assetMap: assetMapReducer,
}));

