/**
 * Test given DmSignState object to determine if it represents a valid sign
 *
 * @param state {DmSignState} - state to test
 * @returns {BsDmError | null} null if sign valid, BsDmError object with message if sign is not valid
 */
export function dmCheckForInvalidDmSignState(state: DmSignState): BsDmError | null {
  if (typeof state === 'object'
    && state.hasOwnProperty('sign')
    && isValidSignState(state.sign)
    && state.hasOwnProperty('zones')
    && isValidZoneState(state.zones)
    && state.hasOwnProperty('mediaStates')
    && isValidMediaStateState(state.mediaStates)
    && state.hasOwnProperty('events')
    && isValidEventState(state.events)
    && state.hasOwnProperty('transitions')
    && isValidTransitionState(state.transitions)
    && state.hasOwnProperty('assetMap')
    && isValidAssetMapState(state.assetMap)) {
    return null;
  } else {
    return new BsDmError(DmErrorType.invalidSign, 'DmState structure is invalid');
  }
}



