import { newBsDmId } from '../utils';

import { HtmlSiteType } from '@brightsign/bscore';
import { BsDmId, DmParameterizedString } from '../bsDmInterfaces';
import { dmResolveDmParameterizedString } from '../bsDmParameterizedStrings';
import { BsDmAction } from './actionBase';

// Actions
/** @internal */
/** @private */
export const ADD_HTML_SITE = 'DM_ADD_HTML_SITE';
/** @internal */
/** @private */
export const UPDATE_HTML_SITE = 'DM_UPDATE_HTML_SITE';
/** @internal */
/** @private */
export const DELETE_HTML_SITE = 'DM_DELETE_HTML_SITE';

export interface HtmlSiteBaseParams {
  id: BsDmId;
  type?: HtmlSiteType;
  name?: string;
  queryString?: DmParameterizedString | string;
}
export interface HtmlSiteLocalParams extends HtmlSiteBaseParams {
  indexAssetId?: string;
}
export interface HtmlSiteRemoteParams extends HtmlSiteBaseParams {
  url?: string;
}
export interface HtmlSiteBrightPlateParams extends HtmlSiteBaseParams {
  brightPlateName?: string;
  displayName?: string;
}
export type HtmlSiteParams = HtmlSiteLocalParams | HtmlSiteRemoteParams | HtmlSiteBrightPlateParams;

export type HtmlSiteLocalAction = BsDmAction<HtmlSiteLocalParams>;
export type HtmlSiteRemoteAction = BsDmAction<HtmlSiteRemoteParams>;
export type HtmlSiteBrightPlateAction = BsDmAction<HtmlSiteBrightPlateParams>;
export type HtmlSiteAction = BsDmAction<HtmlSiteParams>;

export function dmAddLocalHtmlSite(
  name: string,
  indexAssetId: string,
  queryString?: DmParameterizedString | string,
): HtmlSiteLocalAction {
  return {
    type: ADD_HTML_SITE,
    payload: {
      id: newBsDmId(),
      type: HtmlSiteType.Local,
      name,
      indexAssetId,
      queryString: dmResolveDmParameterizedString(queryString),
    },
  };
}

export function dmAddRemoteHtmlSite(
  name: string,
  url: string,
  queryString?: DmParameterizedString | string,
): HtmlSiteRemoteAction {
  return {
    type: ADD_HTML_SITE,
    payload: {
      id: newBsDmId(),
      type: HtmlSiteType.Remote,
      name,
      url,
      queryString: dmResolveDmParameterizedString(queryString),
    },
  };
}

export function dmAddBrightPlateHtmlSite(
  name: string,
  brightPlateName: string,
  displayName: string,
  queryString?: DmParameterizedString | string,
): HtmlSiteBrightPlateAction {
  return {
    type: ADD_HTML_SITE,
    payload: {
      id: newBsDmId(),
      type: HtmlSiteType.BrightPlate,
      name,
      brightPlateName,
      displayName,
      queryString: dmResolveDmParameterizedString(queryString),
    },
  };
}

export function dmUpdateHtmlSite(params: HtmlSiteParams): HtmlSiteAction {
  let localParams = params;
  if (params.hasOwnProperty('queryString')) {
    localParams = Object.assign({}, params, {queryString: dmResolveDmParameterizedString(params.queryString)});
  }
  return {
    type: UPDATE_HTML_SITE,
    payload: localParams,
  };
}

export function dmDeleteHtmlSite(id: BsDmId): HtmlSiteAction {
  return {
    type: DELETE_HTML_SITE,
    payload: {id},
  };
}



