/** @module Actions:MediaState */
/**
 * Created by jsugg on 10/11/16.
 */

import { newBsDmId } from '../utils';

import { BsAssetItem, BsAssetId, bscGenerateAssetLocator, bscIsAssetItem } from '@brightsign/bscore';
import {
  BsDmId, DmMediaStateContainer,
  DmDerivedContentItem, DmDerivedNonMediaContentItem, DmContentItemData,
} from '../bsDmInterfaces';
import {
  dmCreateMediaContentItem, dmContentItemIsMediaContentItem, dmIsValidContentItemForZone, dmFilterContentItemData,
} from '../bsDmContentItems';
import {
  DmState, dmGetZoneTypeById, dmGetMediaStateStateById, dmGetEventIdsForMediaState, dmGetTransitionIdsForEvent,
} from '../reducers/index';

import {
  BsDmAction, BsDmBaseAction, BsDmThunkAction, dmFilterDmState, dmBatchActions,
} from './actionBase';
import { dmSetAssetId, dmPutAssetItemWithoutTest, dmRemoveAssetItems } from './actionAssetMap';
import { DmErrorType, BsDmError } from '../bsDmError';

// Actions
/** @internal */
/** @private */
export const ADD_MEDIA_STATE = 'DM_ADD_MEDIA_STATE';
/** @internal */
/** @private */
export const UPDATE_MEDIA_STATE = 'DM_UPDATE_MEDIA_STATE';
/** @internal */
/** @private */
export const DELETE_MEDIA_STATE = 'DM_DELETE_MEDIA_STATE';

export interface MediaStateParams {
  id: BsDmId;
  name?: string;
  container?: DmMediaStateContainer;
  contentItem?: DmDerivedContentItem;
}
export type MediaStateAction = BsDmAction<MediaStateParams>;

// TODO: important! - add mechanism to disambiguate names (i.e., no duplicate names)
// TODO: test mediaState compatibility to zone and fail if incompatible contentItem

/** @internal */
/** @private */
export const dmContentIsAssetItem = (content: DmDerivedContentItem | BsAssetItem): content is BsAssetItem => {
  return bscIsAssetItem(content);
};

/**
 * Return simple action to add a MediaState given a content item.
 * This should not be used by clients to add a MediaState based on a content file.
 * Clients should use dmAddMediaState to add MediaContentStates use a BsAssetItem.
 *
 * @param name {string} - name for the state - if empty, default name will be supplied
 * @param container {DmMediaStateContainer} - container for the state
 * @param contentItem {DmDerivedContentItem} - the content item for the state
 * @returns {MediaStateAction}
 */
export function dmAddMediaStateWithContentItem(
  name: string,
  container: DmMediaStateContainer,
  contentItem: DmDerivedContentItem,
): MediaStateAction {
  return {
    type: ADD_MEDIA_STATE,
    payload: {
      id: newBsDmId(),
      name: name ? name : contentItem.name,
      container,
      contentItem,
    },
  };
}

/**
 * Return simple action to add a MediaState given a content item or an assetItem.
 * To add a state with a MediaContentItem (i.e., a media file), supply an AssetItem as the content parameter.
 * To add a state with a non-media ContentItem, supply the ContentItem directtly as the content parameter,
 *
 * @param name {string} - name for the state - if empty, default name will be supplied
 * @param container {DmMediaStateContainer} - container for the state
 * @param content {DmDerivedNonMediaContentItem | BsAssetItem} - the content for the state
 * @param [contentData=default values] {DmContentItemData} - contains values for one or more contentItem
 *   properties. This can be used to set ancillary contentItem properties when the 'content' param
 *   is an assetItem.
 * @returns {BsDmThunkAction<MediaStateAction>}
 *   When dispatched, the thunk function will return a MediaStateAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used, including a completed
 *   contentItem, if one was generated for the BsAssetItem.
 *   If the function fails, a BsDmError is thrown, with the action attached.
 */
export function dmAddMediaState(
  name: string,
  container: DmMediaStateContainer,
  content: DmDerivedNonMediaContentItem | BsAssetItem,
  contentData?: Partial<DmContentItemData>,
): BsDmThunkAction<MediaStateParams> {
  return (dispatch, getState): MediaStateAction => {
    const mainAction: MediaStateAction = {
      type: ADD_MEDIA_STATE,
      payload: {
        id: newBsDmId(),
        name,
        container,
      },
    };
    const batchedActions: BsDmBaseAction[] = [];
    const dmState = dmFilterDmState(getState());
    let assetItem: BsAssetItem | undefined;
    let contentItem;
    if (dmContentIsAssetItem(content)) {
      const locator = bscGenerateAssetLocator(content);
      if (!locator) {
        // If locator isn't valid, the assetItem isn't valid
        throw new BsDmError(DmErrorType.invalidParameters,
          'Asset Item does not contain valid locator information', mainAction);
      }
      // Match or create assetId, set that to assetItem, along with generated locator
      assetItem = Object.assign({}, content, {locator});
      dmSetAssetId(dmState, assetItem);
      contentItem = dmCreateMediaContentItem(assetItem.name, assetItem.id, assetItem.mediaType);

      const assetAction = dmPutAssetItemWithoutTest(assetItem);
      batchedActions.push(assetAction);
    } else {
      // If a contentItem, reject if it is a MediaContentItem
      if (dmContentItemIsMediaContentItem(content)) {
        mainAction.payload.contentItem = content;
        throw new BsDmError(DmErrorType.invalidParameters,
          'MediaContentItems must be specified by AssetItem parameters', mainAction);
      }
      contentItem = content;
    }
    const contentItemData = dmFilterContentItemData(contentItem.type, contentData);
    if (contentItemData) {
      contentItem = Object.assign({}, contentItem, contentItemData);
    }
    if (!mainAction.payload.name) {
      mainAction.payload.name = contentItem.name;
    }
    const zoneType = dmGetZoneTypeById(dmState, {id: container.id});
    if (!dmIsValidContentItemForZone(contentItem, zoneType)) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Content item type "' + contentItem.type + '" cannot be added to "' + zoneType + '" zone', mainAction);
    }
    mainAction.payload.contentItem = contentItem;
    batchedActions.push(mainAction);

    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

export function dmUpdateMediaState(params: MediaStateParams): MediaStateAction {
  return {
    type: UPDATE_MEDIA_STATE,
    payload: params,
  };
}

export interface MediaStateDeleteParams {
  mediaStateIds: BsDmId[];
  eventIds: BsDmId[];
  transitionIds: BsDmId[];
}
export type MediaStateDeleteAction = BsDmAction<MediaStateDeleteParams>;

export function dmGetMediaStateDeleteParams(state: DmState, id: (BsDmId | BsDmId[])): MediaStateDeleteParams {
  const deleteParams: MediaStateDeleteParams = {
    mediaStateIds: Array.isArray(id) ? id : [id],
    eventIds: [],
    transitionIds: [],
  };
  // Return IDs for all given mediaStates, plus events and transitions associated with them
  // All will be deleted
  deleteParams.mediaStateIds.forEach((mediaStateId) => {
    const mediaStateEventIds = dmGetEventIdsForMediaState(state, {id: mediaStateId});
    if (mediaStateEventIds) {
      deleteParams.eventIds.splice(0, 0, ...mediaStateEventIds);
    }
  });
  deleteParams.eventIds.forEach((eventId) => {
    const eventTransitionIds = dmGetTransitionIdsForEvent(state, {id: eventId});
    if (eventTransitionIds) {
      deleteParams.transitionIds.splice(0, 0, ...eventTransitionIds);
    }
  });
  return deleteParams;
}

export function dmDeleteMediaStateWithParams(params: MediaStateDeleteParams): MediaStateDeleteAction {
  return {
    type: DELETE_MEDIA_STATE,
    payload: params,
  };
}

export function dmDeleteMediaState(id: BsDmId | BsDmId[]): BsDmThunkAction<MediaStateDeleteParams> {
  return (dispatch, getState): MediaStateDeleteAction  => {
    const dmState = dmFilterDmState(getState());

    // Find any media assets used in the deleted states and remove the references for them in the AssetMap
    const stateIds = typeof id === 'string' ? [id] : id;
    const assetIds: BsAssetId[] = [];
    stateIds.forEach((assetId) => {
      const mediaStateState = dmGetMediaStateStateById(dmState, {id: assetId});
      if (mediaStateState && dmContentItemIsMediaContentItem(mediaStateState.contentItem)) {
        assetIds.push(mediaStateState.contentItem.assetId);
      }
    });
    // Find IDs for all given mediaStates, plus events and transitions associated with them
    // All will be deleted
    const deleteParams = dmGetMediaStateDeleteParams(dmFilterDmState(getState()), id);
    const batchedActions: BsDmBaseAction[] = [];
    if (assetIds.length > 0) {
      batchedActions.push(dmRemoveAssetItems(assetIds));
    }
    const mainAction = dmDeleteMediaStateWithParams(deleteParams);
    batchedActions.push(mainAction);
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}



