import { createSelector } from 'reselect';
import { DmState } from '../reducers/index';
import { BsAssetItem, ZoneType } from '@brightsign/bscore';
import {
  BsDmId, DmIdParam, DmNameParam, DmTypeParam, DmZone,
  DmMediaState, DmEvent, DmTransition,
} from '../bsDmInterfaces';
import {
  dmContentItemIsMediaContentItem, dmIsValidContentItemTypeForZone, dmIsValidContentItemTypeForPlaylist,
} from '../bsDmContentItems';
import {
  DmMediaStateCollectionState, getMediaStateStateById, getMediaStateStateByName,
} from '../reducers/reducerMediaState';
import { DmEventCollectionState, getEventStateById } from '../reducers/reducerEvent';
import { DmTransitionCollectionState } from '../reducers/reducerTransition';
import { DmAssetMapState } from '../reducers/reducerAssetMap';
import { DmcMediaState, DmcEvent, DmDeNormalizedEvent } from '../bsDmClasses';

/** @internal */
/** @private */
export const createMediaStateObject = (
  mediaStateState: DmMediaState,
  events: DmEventCollectionState,
  transitions: DmTransitionCollectionState,
  assetMap: DmAssetMapState,
): DmcMediaState | null => {
  if (mediaStateState) {
    let assetItem: BsAssetItem;
    const deNormEvents: DmDeNormalizedEvent[] = [];
    const eventIds = Object.keys(events).filter((id, index, idArray): boolean =>
      events[id].mediaStateId === mediaStateState.id);
    if (eventIds.length > 0) {
      eventIds.forEach((eventId) => {
        const deNormEvent: DmDeNormalizedEvent = {eventState: events[eventId]};
        const transitionStates = getSortedTransitionStatesForEventId(eventId, transitions);
        if (transitionStates) {
          deNormEvent.transitionStates = transitionStates;
        }
        deNormEvents.push(deNormEvent);
      });
    }
    if (dmContentItemIsMediaContentItem(mediaStateState.contentItem)) {
      assetItem = assetMap[mediaStateState.contentItem.assetId];
    }

    return new DmcMediaState(mediaStateState, deNormEvents, assetItem);
  }
  return null;
};

/** @internal */
/** @private */
export const createEventObject = (
  eventState: DmEvent,
  transitions: DmTransitionCollectionState,
): DmcEvent | null => {
  if (eventState) {
    const transitionStates = getSortedTransitionStatesForEventId(eventState.id, transitions);
    return new DmcEvent(eventState, transitionStates);
  }
  return null;
};

/** @internal */
/** @private */
const getSortedTransitionStatesForEventId = (
  eventId: BsDmId,
  transitions: DmTransitionCollectionState,
): DmTransition[] | null => {
  const transitionStates: DmTransition[] = [];
  const transitionIds = Object.keys(transitions).filter((id, index, idArray): boolean =>
    transitions[id].eventId === eventId);
  if (transitionIds.length > 0) {
    transitionIds.forEach((transitionId) => {
      transitionStates.push(transitions[transitionId]);
    });
    // TODO: sort transitions by condition priority, default should be last
  }
  return transitionStates.length > 0 ? transitionStates : null;
};

/** @internal */
/** @private */
export const getInitialMediaStateIdForZone = createSelector<DmState, BsDmId | null, DmZone> (
  (state: DmState, props: DmIdParam) => state.zones.zonesById[props.id],
  (zone) => zone ? zone.initialMediaStateId : null,
);

/** @internal */
/** @private */
export const getInitialMediaStateForZone = createSelector<
  DmState, DmcMediaState | null, DmZone, DmMediaStateCollectionState, DmEventCollectionState,
  DmTransitionCollectionState, DmAssetMapState>
(
  (state: DmState, props: DmIdParam) => state.zones.zonesById[props.id],
  (state: DmState) => state.mediaStates,
  (state: DmState) => state.events,
  (state: DmState) => state.transitions,
  (state: DmState) => state.assetMap,
  (zone, mediaStates, events, transitions, assetMap) => {
    if (zone) {
      const mediaStateState = mediaStates[zone.initialMediaStateId];
      if (mediaStateState) {
        return createMediaStateObject(mediaStateState, events, transitions, assetMap);
      }
    }
    return null;
  },
);

/** @internal */
/** @private */
export const getMediaStateById = createSelector<
  DmState, DmcMediaState | null, DmMediaState, DmEventCollectionState, DmTransitionCollectionState, DmAssetMapState>
(
  (state: DmState, props: DmIdParam) => getMediaStateStateById(state.mediaStates, props),
  (state: DmState) => state.events,
  (state: DmState) => state.transitions,
  (state: DmState) => state.assetMap,
  createMediaStateObject,
);

/** @internal */
/** @private */
export const getMediaStateByName = createSelector<
  DmState, DmcMediaState | null, DmMediaState, DmEventCollectionState, DmTransitionCollectionState, DmAssetMapState>
(
  (state: DmState, props: DmNameParam) => getMediaStateStateByName(state.mediaStates, props),
  (state: DmState) => state.events,
  (state: DmState) => state.transitions,
  (state: DmState) => state.assetMap,
  createMediaStateObject,
);

/** @internal */
/** @private */
export const getEventById = createSelector<DmState, DmcEvent | null, DmEvent, DmTransitionCollectionState> (
  (state: DmState, props: DmIdParam) => getEventStateById(state.events, props),
  (state: DmState) => state.transitions,
  createEventObject,
);

/** @internal */
/** @private */
export const canChangeZoneType = createSelector<DmState, boolean, ZoneType, DmZone, DmMediaStateCollectionState>(
  (_, props: DmTypeParam) => props.type,
  (state: DmState, props: DmIdParam) => state.zones.zonesById[props.id],
  (state: DmState) => state.mediaStates,
  (targetZoneType, zone, mediaStates): boolean => {
    // The 'some' function will return true if any mediaStates are found that are in the zone, and which have
    //  a content item not compatible with the zone.
    return zone.type === targetZoneType
      || !Object.keys(mediaStates).some((mediaStateId) => {
        const mediaState = mediaStates[mediaStateId];
        return mediaState.container.id === zone.id
          && !(zone.nonInteractive ? dmIsValidContentItemTypeForPlaylist(mediaState.contentItem.type, targetZoneType) :
            dmIsValidContentItemTypeForZone(mediaState.contentItem.type, targetZoneType));
      });
  },
);




