export enum FsErrorType {
  fsNotAvailableError,
  fileNotFoundError,
}

const fsErrorMessage: {[type: number]: string} = {
  [FsErrorType.fsNotAvailableError]: 'Local file system is not accessible',
  [FsErrorType.fileNotFoundError]: 'File not found',
};

export class FsError extends Error {
  name = 'FsError';
  type: FsErrorType;

  constructor(type: FsErrorType, reason?: string) {
    super();
    this.type = type;
    if (reason) {
      this.message = fsErrorMessage[type] + ': ' + reason;
    } else {
      this.message = fsErrorMessage[type];
    }
    Object.setPrototypeOf(this, FsError.prototype);
  }
}



