/** @private */
export const initialSerialPortDefaults: DmSerialPortConfiguration = {
  port: 0,
  baudRate: 115200,
  dataBits: 8,
  stopBits: 1,
  parity: 'N',
  protocol: 'ASCII',
  sendEol: 'CR',
  receiveEol: 'CR',
  invertSignals: false,
  connectedDevice: 'None',
};
Object.freeze(initialSerialPortDefaults);

/** @internal */
/** @private */
const createDefaultSerialPortList = (): DmSerialPortList => {
  const portList: DmSerialPortList = [];
  for (let port = 0; port < 8; ++port) {
    portList.push(Object.assign({}, initialSerialPortDefaults, {port}));
  }
  return portList;
};

