/* tslint:disable:no-namespace */  // TODO - remove namespace as we move to DmcClasses
/** @internal */
/** @private */
export namespace DmInternal {

  export class DefaultProperties implements DmDefaultProperties {

    _signProperties: DmSignPropertyData;
    _serialPortConfigurations: DmSerialPortList;
    _gpioList: DmGpioList;
    _buttonPanels: DmButtonPanelMap;
    _audioSignPropertyMap: DmAudioSignPropertyMap;

    _audioZoneProperties: DmAudioZonePropertyData;
    _enhancedAudioZoneProperties: DmEnhancedAudioZonePropertyData;
    _imageZoneProperties: DmImageZonePropertyData;
    _videoZoneProperties: DmVideoZonePropertyData;
    _tickerZoneProperties: DmTickerZonePropertyData;
    _clockZoneProperties: DmClockZonePropertyData;

    _videoContentItem: DmVideoContentItemData;
    _audioContentItem: DmAudioContentItemData;
    _imageContentItem: DmImageContentItemDefaults;
    _htmlContentItem: DmHtmlContentItemDefaults;
    _mrssContentItem: DmMrssDataFeedContentItemData;
    _liveVideoContentItem: DmLiveVideoContentItemDefaults;
    _videoStreamContentItem: DmStreamContentItemDefaults;
    _audioStreamContentItem: DmStreamContentItemDefaults;

    constructor() {
      this.reset();
    }

    reset() {
      this._signProperties = Object.assign({}, initialSignPropertyDefaults);
      this._serialPortConfigurations = createDefaultSerialPortList();
      this._gpioList = initialGpioListDefaults.slice();
      this._buttonPanels = createDefaultButtonPanelMap();
      this._audioSignPropertyMap = createDefaultAudioSignPropertyMap();

      this._audioZoneProperties = Object.assign({}, initialAudioZonePropertyDefaults);
      this._enhancedAudioZoneProperties = Object.assign({}, initialEnhancedAudioZonePropertyDefaults);
      this._imageZoneProperties = Object.assign({}, initialImageZonePropertyDefaults);
      this._videoZoneProperties = Object.assign({}, initialVideoZonePropertyDefaults);
      this._tickerZoneProperties = Object.assign({}, initialTickerZonePropertyDefaults);
      this._clockZoneProperties = Object.assign({}, initialClockZonePropertyDefaults);

      this._videoContentItem = Object.assign({}, initialVideoContentItemDefaults);
      this._audioContentItem = Object.assign({}, initialAudioContentItemDefaults);
      this._imageContentItem = Object.assign({}, initialImageContentItemDefaults);
      this._htmlContentItem = Object.assign({}, initialHtmlContentItemDefaults);
      this._mrssContentItem = Object.assign({}, initialMrssContentItemDefaults);
      this._liveVideoContentItem = Object.assign({}, initialLiveVideoContentItemDefaults);
      this._videoStreamContentItem = Object.assign({}, initialStreamContentItemDefaults);
      this._audioStreamContentItem = Object.assign({}, initialStreamContentItemDefaults);
    }

    merge(params: DmSetDefaultParams): void {
      // TODO: Verify integrity of incoming default params ??
      if (params.signProperties) {
        Object.assign(this._signProperties, params.signProperties);
      }
      if (params.serialPortConfigurations && Array.isArray(params.serialPortConfigurations)) {
        const updateList = params.serialPortConfigurations;
        updateList.forEach((portConfig: DmSerialPortConfiguration) => {
          const portNum = portConfig.port;
          // If port property is missing, ignore the portConfig object
          if (portNum != null) {
            this._serialPortConfigurations[portNum] =
              Object.assign({}, this._serialPortConfigurations[portNum], portConfig);
          }
        });
      }
      if (params.gpio && Array.isArray(params.gpio)) {
        this._gpioList = params.gpio.slice();
      }
      if (params.buttonPanels) {
        const updateList = params.buttonPanels;
        Object.keys(updateList).forEach((bpName: string) => {
          this._buttonPanels[bpName] = Object.assign({}, this._buttonPanels[bpName], updateList[bpName]);
        });
      }
      if (params.audioSignPropertyMap) {
        const updateList = params.audioSignPropertyMap;
        Object.keys(updateList).forEach((audioName: string) => {
          this._audioSignPropertyMap[audioName] =
            Object.assign({}, this._audioSignPropertyMap[audioName], updateList[audioName]);
        });
      }
      if (params.audioZoneProperties) {
        Object.assign(this._audioZoneProperties, params.audioZoneProperties);
      }
      if (params.enhancedAudioZoneProperties) {
        Object.assign(this._enhancedAudioZoneProperties, params.enhancedAudioZoneProperties);
      }
      if (params.imageZoneProperties) {
        Object.assign(this._imageZoneProperties, params.imageZoneProperties);
      }
      if (params.videoZoneProperties) {
        Object.assign(this._videoZoneProperties, params.videoZoneProperties);
      }
      if (params.tickerZoneProperties) {
        const newProps = Object.assign({}, params.tickerZoneProperties);
        if (params.tickerZoneProperties.textWidget) {
          newProps.textWidget =
            Object.assign({}, this._tickerZoneProperties.textWidget, params.tickerZoneProperties.textWidget);
        }
        if (params.tickerZoneProperties.widget) {
          const widgetClone = cloneDeep(params.tickerZoneProperties.widget);
          newProps.widget = Object.assign({}, this._tickerZoneProperties.widget, widgetClone);
        }
        Object.assign(this._tickerZoneProperties, newProps);
      }
      if (params.clockZoneProperties) {
        const newProps = Object.assign({}, params.clockZoneProperties);
        if (params.clockZoneProperties.widget) {
          const widgetClone = cloneDeep(params.clockZoneProperties.widget);
          newProps.widget = Object.assign({}, this._clockZoneProperties.widget, widgetClone);
        }
        Object.assign(this._clockZoneProperties, newProps);
      }
      if (params.videoContentItem) {
        Object.assign(this._videoContentItem, params.videoContentItem);
      }
      if (params.audioContentItem) {
        Object.assign(this._audioContentItem, params.audioContentItem);
      }
      if (params.imageContentItem) {
        Object.assign(this._imageContentItem, params.imageContentItem);
      }
      if (params.htmlContentItem) {
        Object.assign(this._htmlContentItem, params.htmlContentItem);
      }
      if (params.mrssContentItem) {
        Object.assign(this._mrssContentItem, params.mrssContentItem);
      }
      if (params.liveVideoContentItem) {
        Object.assign(this._liveVideoContentItem, params.liveVideoContentItem);
      }
      if (params.videoStreamContentItem) {
        Object.assign(this._videoStreamContentItem, params.videoStreamContentItem);
      }
      if (params.audioStreamContentItem) {
        Object.assign(this._audioStreamContentItem, params.audioStreamContentItem);
      }
    }

    get allDefaults(): DmDefaultProperties {
      return {
        signProperties: this.signProperties,
        serialPortConfigurations: this.serialPortConfigurations,
        gpio: this.gpio,
        buttonPanels: this.buttonPanels,
        audioSignPropertyMap: this.audioSignPropertyMap,
        audioZoneProperties: this.audioZoneProperties,
        enhancedAudioZoneProperties: this.enhancedAudioZoneProperties,
        imageZoneProperties: this.imageZoneProperties,
        videoZoneProperties: this.videoZoneProperties,
        tickerZoneProperties: this.tickerZoneProperties,
        clockZoneProperties: this.clockZoneProperties,
        videoContentItem: this.videoContentItem,
        audioContentItem: this.audioContentItem,
        imageContentItem: this.imageContentItem,
        htmlContentItem: this.htmlContentItem,
        mrssContentItem: this.mrssContentItem,
        liveVideoContentItem: this.liveVideoContentItem,
        videoStreamContentItem: this.videoStreamContentItem,
        audioStreamContentItem: this.audioStreamContentItem,
      };
    }

    get signProperties() { return cloneDeep(this._signProperties); }
    get serialPortConfigurations() { return cloneDeep(this._serialPortConfigurations); }
    get gpio() { return cloneDeep(this._gpioList); }
    get buttonPanels() { return cloneDeep(this._buttonPanels); }
    get audioSignPropertyMap() { return cloneDeep(this._audioSignPropertyMap); }
    get audioZoneProperties() { return cloneDeep(this._audioZoneProperties); }
    get enhancedAudioZoneProperties() { return cloneDeep(this._enhancedAudioZoneProperties); }
    get imageZoneProperties() { return cloneDeep(this._imageZoneProperties); }
    get videoZoneProperties() { return cloneDeep(this._videoZoneProperties); }
    get tickerZoneProperties() { return cloneDeep(this._tickerZoneProperties); }
    get clockZoneProperties() { return cloneDeep(this._clockZoneProperties); }
    get videoContentItem() { return cloneDeep(this._videoContentItem); }
    get audioContentItem() { return cloneDeep(this._audioContentItem); }
    get imageContentItem() { return cloneDeep(this._imageContentItem); }
    get htmlContentItem() { return cloneDeep(this._htmlContentItem); }
    get mrssContentItem() { return cloneDeep(this._mrssContentItem); }
    get liveVideoContentItem() { return cloneDeep(this._liveVideoContentItem); }
    get videoStreamContentItem() { return cloneDeep(this._videoStreamContentItem); }
    get audioStreamContentItem() { return cloneDeep(this._audioStreamContentItem); }
  }

  export let defaults = new DefaultProperties();
}



