/** @module Actions:Zone */
/**
 * Created by jsugg on 10/11/16.
 */

import { newBsDmId } from '../utils';

import { ZoneType, BsRect } from '@brightsign/bscore';
import {
  BsDmId, DmAudioZoneProperties, DmVideoZoneProperties, DmImageZoneProperties,
  DmVideoOrImagesZoneProperties, DmEnhancedAudioZoneProperties, DmTickerZoneProperties, DmClockZoneProperties,
} from '../bsDmInterfaces';
import { DmErrorType, BsDmError } from '../bsDmError';

import { dmGetZoneTypeById, dmGetMediaStateIdsForZone } from '../reducers/index';
import { dmCanChangeZoneType } from '../selectors/index';

import { createDefaultZoneProperties } from '../reducers/reducerZone';

import { BsDmAction, BsDmThunkAction, dmFilterDmState } from './actionBase';
import { dmGetMediaStateDeleteParams } from './actionMediaState';

import { pick, omit } from 'lodash';

// Actions
/** @internal */
/** @private */
export const ADD_ZONE = 'DM_ADD_ZONE';
/** @internal */
/** @private */
export const UPDATE_ZONE = 'DM_UPDATE_ZONE';
/** @internal */
/** @private */
export const UPDATE_ZONE_PROPS = 'DM_UPDATE_ZONE_PROPS';
/** @internal */
/** @private */
export const DELETE_ZONE = 'DM_DELETE_ZONE';

export interface ZoneParams {
  id: BsDmId;
  name?: string;
  type?: ZoneType;
  tag?: string;
  nonInteractive?: boolean;
  initialMediaStateId?: BsDmId;
  position?: BsRect;
}
export type ZoneAction = BsDmAction<ZoneParams>;

/**
 * Add a zone to the sign.
 *
 * @param name {string} - Name for the new zone
 * @param type {ZoneType} - type of zone to add
 * @param tag {string} - short tag string for zone identification (e.g., 'V1', 'A1')
 * @param position {BsRect} - position and size of zone rectangle on the output display
 * @param nonInteractive {boolean} - true if the zone is intended to be a non-interactive zone
 * @returns {ZoneAction}
 *
 */
export function dmAddZone(
  name: string,
  type: ZoneType,
  tag: string,
  position: BsRect = {x: 0, y: 0, width: 100, height: 100, pct: true},
  nonInteractive = true,
): ZoneAction {
  return {
    type: ADD_ZONE,
    payload: {
      id: newBsDmId(),
      name,
      type,
      tag,
      position,
      nonInteractive,
    },
  };
}

// Used internally to update without using a thunk to test initialMediaState
// Also assumes no zone type change
/** @internal */
/** @private */
export const dmUpdateZoneWithoutTest = (params: ZoneParams): ZoneAction => ({
  type: UPDATE_ZONE,
  payload: params,
});

export interface ZoneChangeParams extends ZoneParams {
  properties: ZonePropertyParams;
}
export type ZoneChangeAction = BsDmAction<ZoneChangeParams>;

/**
 * Update the properties for a given zone.
 * This action creator can be used to change the type of a zone, if the new zone type is compatible with existing
 *  media states within the zone. If it is not, the action will be rejected.
 * If the zone type is changed, the zone specific properties referenced by the 'properties' key will be
 *  updated such that they are appropriate for the new zone. All new zone specific properties will be set
 *  to default values, but values for any properties that existed in the previous zone type will be carried over.
 *
 * If an attempt is made to set the initialMediaState to a state ID representing a state that is not within the zone,
 *  a BsDmError will be thrown.
 *
 * @param params {ZoneParams} - parameters that specify desired updates. Note that the 'properties' property, which
 *  is the key for an object holding zone-specific properties, cannot be specified as a parameter here.
 *  Use {@link dmUpdateZoneProperties} to update zone specific properties.
 * @returns {BsDmThunkAction<ZoneParams | ZoneChangeParams>}
 */
export function dmUpdateZone(params: ZoneParams): BsDmThunkAction<ZoneParams | ZoneChangeParams> {
  return (dispatch, getState): ZoneAction  => {
    const action: ZoneAction = {type: UPDATE_ZONE, payload: params};
    const state = dmFilterDmState(getState());
    // If the update parameters include an update initialMediaStateId, make sure that the ID
    //  actually refers to a media state in the given zone. If not, do not proceed with the update.
    if (params.initialMediaStateId) {
      const mediaStates = dmGetMediaStateIdsForZone(state, {id: params.id});
      if (mediaStates.indexOf(params.initialMediaStateId) < 0) {
        throw new BsDmError(DmErrorType.invalidParameters, 'Invalid initial media state', action);
      }
    }
    // Make sure there is no 'properties' property in the param payload
    if (action.payload.hasOwnProperty('properties')) {
      action.payload = omit(action.payload, 'properties') as ZoneParams;
    }
    // If the params indicate a zoneType change, make sure we can change to the requested zone
    if (params.type) {
      const zoneType = dmGetZoneTypeById(state, {id: params.id});
      if (zoneType !== params.type) {
        if (!dmCanChangeZoneType(state, {id: params.id, type: params.type as string})) {
          throw new BsDmError(DmErrorType.invalidParameters,
            'Zone contains media states not allowed in requested zone type', action);
        }
        // If we are changing zone types, we have to also correctly change zone specific parameters
        // Get zone properties for new zone
        let newZoneProperties = createDefaultZoneProperties(params.type);
        // Carry over any that exist in old zone
        const previousZoneProperties = state.zones.zonesById[params.id].properties;
        const carryOverProperties = pick(previousZoneProperties, Object.keys(newZoneProperties));
        newZoneProperties = Object.assign(newZoneProperties, carryOverProperties);
        action.payload = Object.assign(params, {properties: newZoneProperties});
      }
    }
    return dispatch(action);
  };
}

// 'Partial' is a Typescript 2.1 construct that yields an equivalent interface where all members are optional
export type AudioZonePropertyParams = Partial<DmAudioZoneProperties>;
export type EnhancedAudioZonePropertyParams = Partial<DmEnhancedAudioZoneProperties>;
export type ImageZonePropertyParams = Partial<DmImageZoneProperties>;
export type VideoZonePropertyParams = Partial<DmVideoZoneProperties>;
export type VideoOrImagesZonePropertyParams = Partial<DmVideoOrImagesZoneProperties>;
export type TickerZonePropertyParams = Partial<DmTickerZoneProperties>;
export type ClockZonePropertyParams = Partial<DmClockZoneProperties>;

export type ZonePropertyParams = AudioZonePropertyParams | EnhancedAudioZonePropertyParams
  | ImageZonePropertyParams | VideoZonePropertyParams | VideoOrImagesZonePropertyParams
  | TickerZonePropertyParams | ClockZonePropertyParams;

// Type property must specify the zone type corresponding to the property structure passed
export interface ZonePropertyUpdateParams {
  id: BsDmId;
  type: ZoneType;
  properties: ZonePropertyParams;
}
export type ZonePropertyUpdateAction = BsDmAction<ZonePropertyUpdateParams>;

/**
 * Update zone-specific properties for a given zone.
 * Each zone type has a specific set of properties unique to the zone type. This action is used to update one or
 *  more of the zone-specific properties for the given zone. It is not necessary to specify the entire
 *  set of zone-specific properties - only the ones that need to be changed.
 *
 * The zone type for the given property object must be explicitly included as part of the input parameters.
 *  If this type does not match the given zone ID, the action will be rejected.
 *
 * @param params {ZonePropertyUpdateParams} - parameters specifying the zone ID, the zone type, and a object
 *  containing one or more updated values for the set of zone specific properties for the zone type.
 * @returns {BsDmThunkAction<ZonePropertyUpdateParams>}
 */
export function dmUpdateZoneProperties(params: ZonePropertyUpdateParams): BsDmThunkAction<ZonePropertyUpdateParams> {
  return (dispatch, getState): ZonePropertyUpdateAction  => {
    const action: ZonePropertyUpdateAction = {type: UPDATE_ZONE_PROPS, payload: params};
    const zoneType = dmGetZoneTypeById(dmFilterDmState(getState()), {id: params.id});
    // If the parameter zone type doesn't match this zone, fail
    if (params.type !== zoneType) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Properties are specified for a "' + params.type + '"zone, but this zone is a "'
        + zoneType + '" zone', action);
    } else if (zoneType === ZoneType.BackgroundImage) {
      throw new BsDmError(DmErrorType.invalidParameters, 'BackgroundImage zone has no properties', action);
    }
    return dispatch(action);
  };
}

export interface ZoneDeleteParams {
  zoneId: BsDmId;
  mediaStateIds: BsDmId[];
  eventIds: BsDmId[];
  transitionIds: BsDmId[];
}
export type ZoneDeleteAction = BsDmAction<ZoneDeleteParams>;

export function dmDeleteZoneWithParams(params: ZoneDeleteParams): ZoneDeleteAction {
  return {
    type: DELETE_ZONE,
    payload: params,
  };
}

/**
 * Delete a zone from the sign.
 * All media states contained within the zone, along with any events and transitions contained
 *  within those media states, will also be deleted. The ZoneDeleteParams object returned as the payload within
 *  the returned ZoneDeleteAction contains lists of IDs of all deleted media states, events, and transitions.
 *
 * @param id {BsDmId} - ID of zone to delete
 * @returns {BsDmThunkAction<ZoneDeleteParams>}
 */
export function dmDeleteZone(id: BsDmId): BsDmThunkAction<ZoneDeleteParams> {
  return (dispatch, getState): ZoneDeleteAction  => {
    // Find IDs for all mediaStates, events and transitions associated with the zone
    // They will be deleted also
    const mediaStateIds = dmGetMediaStateIdsForZone(dmFilterDmState(getState()), {id});
    const mediaStateDeleteParams = dmGetMediaStateDeleteParams(dmFilterDmState(getState()), mediaStateIds);
    const zoneDeleteParams: ZoneDeleteParams = Object.assign({zoneId: id}, mediaStateDeleteParams);
    return dispatch(dmDeleteZoneWithParams(zoneDeleteParams));
  };
}



