import { newBsDmId } from '../utils';

import { TransitionType } from '@brightsign/bscore';
import { BsDmId, DmCondition } from '../bsDmInterfaces';

import { BsDmAction } from './actionBase';

// Actions
/** @internal */
/** @private */
export const ADD_TRANSITION = 'DM_ADD_TRANSITION';
/** @internal */
/** @private */
export const UPDATE_TRANSITION = 'DM_UPDATE_TRANSITION';
/** @internal */
/** @private */
export const DELETE_TRANSITION = 'DM_DELETE_TRANSITION';

export interface TransitionParams {
  id: BsDmId;
  name?: string;
  eventId?: BsDmId;
  targetMediaStateId?: BsDmId;
  type?: TransitionType;
  duration?: number;
  condition?: DmCondition;
}
export type TransitionAction = BsDmAction<TransitionParams>;

export function dmAddTransition(
  name: string,
  eventId: BsDmId,
  targetMediaStateId: BsDmId,
  type: TransitionType = TransitionType.NoEffect,
  duration: number = 0,
  condition?: DmCondition,
): TransitionAction {
  return {
    type: ADD_TRANSITION,
    payload: {
      id: newBsDmId(),
      name,
      eventId,
      targetMediaStateId,
      type,
      duration,
      condition,
    },
  };
}

export function dmUpdateTransition(params: TransitionParams): TransitionAction {
  return {
    type: UPDATE_TRANSITION,
    payload: params,
  };
}

export function dmDeleteTransition(id: BsDmId): TransitionAction {
  return {
    type: DELETE_TRANSITION,
    payload: {id},
  };
}



