import { newBsDmId } from '../utils';

import { EventType } from '@brightsign/bscore';
import { BsDmId, DmEventData } from '../bsDmInterfaces';

import { BsDmAction, BsDmActionCreator } from './actionBase';

// Actions
/** @internal */
/** @private */
export const ADD_EVENT = 'DM_ADD_EVENT';
/** @internal */
/** @private */
export const UPDATE_EVENT = 'DM_UPDATE_EVENT';
/** @internal */
/** @private */
export const DELETE_EVENT = 'DM_DELETE_EVENT';

export interface EventParams {
  id: BsDmId;
  name?: string;
  type?: EventType;
  mediaStateId?: BsDmId;
  data?: DmEventData;
}
export type EventAction = BsDmAction<EventParams>;

export function dmAddEvent(name: string, type: EventType, mediaStateId: BsDmId, data?: DmEventData): EventAction {
  return {
    type: ADD_EVENT,
    payload: {
      id: newBsDmId(),
      name,
      type,
      mediaStateId,
      data,
    },
  };
}

export function dmUpdateEvent(params: EventParams): EventAction {
  return {
    type: UPDATE_EVENT,
    payload: params,
  };
}

export function dmDeleteEvent(id: BsDmId): EventAction {
  return {
    type: DELETE_EVENT,
    payload: {id},
  };
}



