import {BsAssetItem, AssetType} from '@brightsign/bscore';
import * as fse from 'fs-extra';
import * as path from 'path';
import {getBscFileTypeInfo, getAssetItemFromFileStats} from './fileOperations';

export function isLocalFileSystemAvailable() {
  return true;
}

export class FsFolderEnumerator {

  // region Public fields
  /**
   * @property defaultRootPath {string} - Base path for all enumerations
   */
  defaultRootPath: string;
  // endregion

  // region Private fields
  /** @internal */
  private _assetTypeSet: Set<AssetType> | null = null;
  // endregion

  // region Public Methods
  constructor(basePath?: string) {
    this.defaultRootPath = basePath ? basePath : '';
  }

  /**
   * Update content directory from local file system and resolve with list of content names
   *
   * @param [directoryPath] {string} - local path of directory to resolve, relative to defaultRootPath.
   *  If path is blank or undefined, the root path is assumed.
   * @param [assetTypes] {AssetType[]} - array of asset types to include. If undefined or empty, all files
   *  and folders are included.
   * @returns {Promise<BsAssetItem[]>} - Promise that resolves with an array of assetItems
   */
  update(directoryPath?: string, assetTypes?: AssetType | AssetType[]): Promise<BsAssetItem[]> {
    this._assetTypeSet = null;
    if (assetTypes) {
      this._assetTypeSet = Array.isArray(assetTypes) ? new Set(assetTypes) : new Set([assetTypes]);
    }
    return new Promise<BsAssetItem[]>((resolve, reject) => {

      const basePath = directoryPath ? path.join(this.defaultRootPath, directoryPath) : this.defaultRootPath;
      fse.readdir(basePath)
        .then((fileNames) => {
          const updateList: Array<Promise<BsAssetItem>> = [];
          fileNames.forEach((fileName) => {
            // Ignore hidden files
            if (fileName.charAt(0) !== '.') {
              updateList.push(this.getLocalBsAssetItem(fileName, basePath));
            }
          });
          return Promise.all(updateList);
        })
        .then((assetItems) => {
          // TODO: get thumbnails
          resolve(assetItems.filter((assetItem) => {
            return assetItem !== null;
          }));
        })
        .catch((error) => { reject(error); });
    });
  }
  // endregion

  // region Private methods
  /** @internal */
  /** @private */
  private getLocalBsAssetItem(name: string, dirPath: string): Promise<BsAssetItem | null> {
    return new Promise<BsAssetItem>((resolve, reject) => {
      const fullPath = path.join(dirPath, name);
      fse.stat(fullPath)
        .then((stats) => {
          const typeInfo = getBscFileTypeInfo(name, stats);
          if (this._assetTypeSet && this._assetTypeSet.has(typeInfo.assetType)) {
            resolve(getAssetItemFromFileStats(name, dirPath, stats));
          } else {
            resolve(null);
          }
        })
        .catch((error) => { reject(error); });
    });
  }
  // endregion
}




