import {v4} from 'uuid';

/** @internal */
/** @private */
export const newBsDmId = () => v4();

const reValidId = /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i;
/** @internal */
/** @private */
export const isValidBsDmId = (id: string): boolean => {
  const ret = id.match(reValidId);
  return ret && id === ret[0];
};



