// Actions
/** @internal */
/** @private */
export const PLAYLIST_ADD_MEDIA_STATE_AT_INDEX = 'DM_PLAYLIST_ADD_MEDIA_STATE_AT_INDEX';
/** @internal */
/** @private */
export const PLAYLIST_ADD_MEDIA_STATE_RANGE_AT_INDEX = 'DM_PLAYLIST_ADD_MEDIA_STATE_RANGE_AT_INDEX';
/** @internal */
/** @private */
export const PLAYLIST_DELETE_MEDIA_STATE_RANGE = 'DM_PLAYLIST_DELETE_MEDIA_STATE_RANGE';
/** @internal */
/** @private */
export const PLAYLIST_DELETE_MEDIA_STATE_AT_INDICES = 'DM_PLAYLIST_DELETE_MEDIA_STATE_AT_INDICES';
/** @internal */
/** @private */
export const PLAYLIST_MOVE_MEDIA_STATE_RANGE_TO_INDEX = 'DM_PLAYLIST_MOVE_MEDIA_STATE_RANGE_TO_INDEX';
/** @internal */
/** @private */
export const PLAYLIST_MOVE_MEDIA_STATE_INDICES_TO_INDEX = 'DM_PLAYLIST_MOVE_MEDIA_STATE_INDICES_TO_INDEX';

/** @internal */
/** @private */
const getDefaultNameForContentItem = (contentItem: DmDerivedContentItem, index: number): string => {
  return contentItem.name ? contentItem.name : 'State' + index;
};

/** @internal */
/** @private */
const getDefaultEventTypeForContentItem = (contentItem: DmDerivedContentItem): EventType => {
  // Assume it will be a timer
  let eventType = EventType.Timer;
  if (contentItem.type === ContentItemType.Video || contentItem.type === ContentItemType.Audio) {
    // Set it to mediaEnd if it is media with a duration
    eventType = EventType.MediaEnd;
  }
  return eventType;
};

/** @internal */
/** @private */
const getDefaultEventDataForEventType = (eventType: EventType, contentItem: DmDerivedContentItem): any => {
  switch (eventType) {
    case EventType.Timer: {
      let duration = 0;
      switch (contentItem.type) {
        case ContentItemType.Image: {
          const defaults = dmGetDefaultImageContentItemData();
          duration = defaults.displayDuration;
          break;
        }
        case ContentItemType.Html: {
          const defaults = dmGetDefaultHtmlContentItemData();
          duration = defaults.displayDuration;
          break;
        }
        case ContentItemType.LiveVideo: {
          const defaults = dmGetDefaultLiveVideoContentItemData();
          duration = defaults.displayDuration;
          break;
        }
        case ContentItemType.VideoStream: {
          const defaults = dmGetDefaultVideoStreamContentItemData();
          duration = defaults.displayDuration;
          break;
        }
        case ContentItemType.AudioStream: {
          const defaults = dmGetDefaultAudioStreamContentItemData();
          duration = defaults.displayDuration;
          break;
        }
      }
      return { interval: duration };
    }
  }
  return undefined;
};

/** @internal */
/** @private */
const getDefaultTransitionType = (): TransitionType => TransitionType.NoEffect;

/**
 * Payload structure for PlaylistAddMediaStateAction
 *  This is not directly used, since this action is a compound action.
 *  It is returned to detail the payload in the action returned from the action dispatch.
 */
export interface PlaylistAddMediaStateParams {
  id: BsDmId;
  index: number;
  container: DmMediaStateContainer;
  contentItem: DmDerivedContentItem;
  name?: string;
  transitionType?: TransitionType;
  eventType?: EventType;
  eventData?: any;
}
export type PlaylistAddMediaStateAction = BsDmAction<PlaylistAddMediaStateParams>;

/**
 * Add a new MediaState to the playlist at a given index.
 * This function will fail if the MediaState container does not contain a valid simple playlist.
 *
 * @param index {number} - zero based index at which to add new MediaState.
 *   If out of range, MediaState will be appended to the end.
 * @param container {DmMediaStateContainer}- MediaState container
 * @param content {DmDerivedNonMediaContentItem | BsAssetItem} - Content
 *   This can be either a DerivedContentItem (for non media based contentItems,)
 *   or a BsAssetItem (for media file content.) If a BsAssetItem is given, the asset will be added to
 *   the AssetMap if necessary, and a DmMediaContentItem will be generated for the MediaState with the
 *   correct asset ID
 * @param [name=fileName or 'StateX'] {string} - desired mediaState name.
 *   Default: media fileName if contentItem has a media file, "State" + a numeric index otherwise
 * @param [contentData=default values] {DmContentItemData} - contains values for one or more contentItem
 *   properties. This can be used to set ancillary contentItem properties when the 'content' param
 *   is an assetItem.
 * @param [transitionType=NoEffect] {TransitionType} - type of Transition to next state.
 * @param [eventType] {EventType} - type of event that triggers the transition.
 *   Default: MediaEnd for video/audio, Timer for images
 * @param [eventData=undefined] {object} - data for event. For timer events, this be be a DmTimer object
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return a PlaylistAddMediaStateAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used, including substitutions
 *   made for optional input.
 *   If the function fails, a BsDmError is thrown.
 */
// TODO: test mediaState compatibility to zone and fail if incompatible contentItem
export function dmPlaylistAddMediaState(
  index: number,
  container: DmMediaStateContainer,
  content: DmDerivedNonMediaContentItem | BsAssetItem,
  name?: string,
  contentData?: Partial<DmContentItemData>,
  transitionType?: TransitionType,
  eventType?: EventType,
  eventData?: any,
): BsDmThunkAction<PlaylistAddMediaStateParams> {
  return (dispatch, getState): PlaylistAddMediaStateAction  => {
    const newMediaStateId = newBsDmId();
    let contentItem: DmDerivedContentItem;
    let assetItem: BsAssetItem | undefined;
    let invalidContentItem = false;
    if (dmContentIsAssetItem(content)) {
      // Duplicate the asset item, since it will be mutated
      assetItem = Object.assign({}, content);
      contentItem = dmCreateMediaContentItem(assetItem.name, '', assetItem.mediaType);
    } else {
      invalidContentItem = dmContentItemIsMediaContentItem(content);
      contentItem = content;
    }
    const contentItemData = dmFilterContentItemData(contentItem.type, contentData);
    if (contentItemData) {
      contentItem = Object.assign({}, contentItem, contentItemData);
    }
    const mainAction: PlaylistAddMediaStateAction =  {
      type: PLAYLIST_ADD_MEDIA_STATE_AT_INDEX,
      payload: { id: newMediaStateId, index, container, contentItem, name, transitionType, eventType, eventData },
    };
    // If given content is a contentItem, reject if it is a MediaContentItem
    if (invalidContentItem) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'MediaContentItems must be specified by AssetItem parameters', mainAction);
    }
    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }
    const zoneType = dmGetZoneTypeById(dmState, {id: container.id});
    if (!dmIsValidContentItemForPlaylist(contentItem, zoneType)) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Content item type "' + contentItem.type + '" cannot be added to "' + zoneType + '" zone', mainAction);
    }
    // Append mediaState if index in undefined or invalid
    if (index === undefined || index > playlistIds.length || index < 0) {
      index = playlistIds.length;
    }
    // Set defaults for data that is not specified
    if (!name || name === '') {
      mainAction.payload.name = name = getDefaultNameForContentItem(contentItem, playlistIds.length + 1);
    }
    if (!eventType) {
      mainAction.payload.eventType = eventType = getDefaultEventTypeForContentItem(contentItem);
    }
    if (eventType === EventType.Timer && !(eventData && eventData.interval)) {
      mainAction.payload.eventData = eventData = getDefaultEventDataForEventType(eventType, contentItem);
    }
    if (!transitionType) {
      mainAction.payload.transitionType = transitionType = getDefaultTransitionType();
    }

    // Get IDs of previous and next mediaStates in the playlist
    let prevMediaStateId: BsDmId;
    let nextMediaStateId: BsDmId;
    if (playlistIds.length > 1) {
      if (index > 0) {
        prevMediaStateId = playlistIds[index - 1];
      } else {
        prevMediaStateId = playlistIds[playlistIds.length - 1];
      }
      if (index === playlistIds.length) {
        nextMediaStateId = playlistIds[0];
      } else {
        nextMediaStateId = playlistIds[index];
      }
    } else if (playlistIds.length === 1) {
      prevMediaStateId = nextMediaStateId = playlistIds[0];
    } else {
      // This is the first mediaState added, so we add an event with a transition pointing
      //  back to this state
      nextMediaStateId = newMediaStateId;
    }
    // Get the previous media state, if there is one
    let prevMediaState;
    if (prevMediaStateId) {
      prevMediaState = dmGetMediaStateById(dmState, {id: prevMediaStateId});
    }

    const batchedActions: BsDmBaseAction[] = [];
    // If we have an asset item, we need to make sure it is in the assetMap
    if (assetItem) {
      assetItem = dmSetAssetId(dmState, assetItem);
      (contentItem as DmMediaContentItem).assetId = assetItem.id;
      batchedActions.push(dmPutAssetItemWithoutTest(assetItem));
      mainAction.payload.contentItem = contentItem;
    }
    // Add the mediaState to the zone in the data model
    const addMediaStateAction = dmAddMediaStateWithContentItem(name, container, contentItem);
    // Substitute our created ID into the payload
    addMediaStateAction.payload.id = newMediaStateId;
    batchedActions.push(addMediaStateAction);

    // Add event for media state
    const eventAction = dmAddEvent(name + '_ev', eventType, newMediaStateId, eventData);
    const newEventId = eventAction.payload.id;
    batchedActions.push(eventAction);

    // Transition target for the previous media state should change to point to the new state.
    if (prevMediaState) {
      const transitionId = prevMediaState.eventList[0].defaultTransition.id;
      batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId: newMediaStateId}));
    }
    // Add a transition for the event in the new media state to the next media state
    batchedActions.push(dmAddTransition(name + '_tr', newEventId, nextMediaStateId, transitionType));

    if (index === 0) {
      // If new state is placed in first position, update initialMediaStateId for the zone
      // Use our internal function that doesn't use the thunk to test initialMediaState
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: newMediaStateId}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

/**
 * Append a new MediaState to the end of the playlist.
 *  This function will fail if the MediaState container does not contain a valid simple playlist.
 *
 * @param container {DmMediaStateContainer} - MediaState container
 * @param content {DmDerivedNonMediaContentItem | BsAssetItem} - Content
 *   This can be either a DerivedContentItem (for non media based contentItems,)
 *   or a BsAssetItem (for media file content.) If a BsAssetItem is given, the asset will be added to
 *   the AssetMap if necessary, and a DmMediaContentItem will be generated for the MediaState with the
 *   correct asset ID
 * @param [name=fileName or 'StateX'] {string} - desired mediaState name.
 *   Default: media fileName if contentItem has a media file, "State" + a numeric index otherwise
 * @param [contentData=default values] {DmContentItemData} - contains values for one or more contentItem
 *   properties. This can be used to set ancillary contentItem properties when the 'content' param
 *   is an assetItem.
 * @param [transitionType=NoEffect] {TransitionType} - type of Transition to next state.
 * @param [eventType] {EventType} - type of event that triggers the transition.
 *   Default: MediaEnd for video/audio, Timer for images
 * @param [eventData] {object} - data for event. For timer events, this be be a DmTimer object
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return a PlaylistAddMediaStateAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used, including substitutions
 *   made for optional input.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistAppendMediaState(
  container: DmMediaStateContainer,
  content: DmDerivedNonMediaContentItem | BsAssetItem,
  name?: string,
  contentData?: Partial<DmContentItemData>,
  transitionType?: TransitionType,
  eventType?: EventType,
  eventData?: any,
): BsDmThunkAction<PlaylistAddMediaStateParams> {
  return dmPlaylistAddMediaState(-1, container, content, name, contentData, transitionType, eventType, eventData);
}

export interface PlaylistAddMediaStateRangeParams {
  index: number;
  container: DmMediaStateContainer;
  id: BsDmId[];
  contentItems: DmDerivedContentItem[];
  name?: string[];
}
export type PlaylistAddMediaStateRangeAction = BsDmAction<PlaylistAddMediaStateRangeParams>;

/**
 * Add one or more new MediaStates, as a range, at a specific index in the playlist.
 *  This function will fail if the MediaState container does not contain a valid simple playlist.
 *
 * @param index {number} - zero based index at which to add new MediaState.
 *   If out of range, MediaState will be appended to the end.
 * @param container {DmMediaStateContainer} - MediaState container
 * @param content {Array<DmDerivedNonMediaContentItem | BsAssetItem>} - array of Content objects
 *   Each element can be either a DerivedContentItem (for non media based contentItems,)
 *   or a BsAssetItem (for media file content.) If a BsAssetItem is given, the asset will be added to
 *   the AssetMap if necessary, and a DmMediaContentItem will be generated for the MediaState with the
 *   correct asset ID
 * @param [name=fileName or 'StateX'] {string[] | null} - array of desired mediaState name corresponding
 *   to order of contentItem array. Pass null in any position  to indicate the default name should be
 *   used for the corresponding content item.
 *   Default: for any names that are missing, the filename is used if contentItem has a media file,
 *   "State" + a numeric index otherwise
 * @param [contentData=default values] {DmContentItemData[]} - an array of objects, each of which
 *   contains values for one or more contentItem properties associated with the corresponding content
 *   object on the 'content' array parameter. This can be used to set ancillary contentItem properties
 *   when a 'content' param is an assetItem. If default values are desired for one or more of the
 *   content items, set the corresponding member in this array to null.
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return a PlaylistAddMediaStateRangeAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used, including substitutions
 *   made for optional input.
 *   If the function fails, a BsDmError is thrown.
 */
// TODO: test mediaState compatibility to zone and fail if incompatible contentItem is included
export function dmPlaylistAddMediaStateRange(
  index: number,
  container: DmMediaStateContainer,
  content: Array<DmDerivedNonMediaContentItem | BsAssetItem>,
  name?: string[] | null,
  contentData?: Array<Partial<DmContentItemData>>,
): BsDmThunkAction<PlaylistAddMediaStateRangeParams> {
  return (dispatch, getState): PlaylistAddMediaStateRangeAction => {
    const getName = (i) => name && i < name.length ? name[i] : '';
    const mainAction: PlaylistAddMediaStateRangeAction = {
      type: PLAYLIST_ADD_MEDIA_STATE_RANGE_AT_INDEX,
      payload: {
        index,
        container,
        id: [] as BsDmId[],
        name: [] as string[],
        contentItems: [] as DmDerivedContentItem[],
      },
    };
    if (!content || content.length === 0) {
      // Invalid parameters
      throw new BsDmError(DmErrorType.invalidParameters,
        'Invalid parameters for MediaState addition: no content provided', mainAction);
    }

    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }
    const contentItems: DmDerivedContentItem[] = [];
    const assetItems: BsAssetItem[] | undefined = [];
    let invalidContentItem = false;
    content.forEach((item, contentIndex) => {
      let contentItem: DmDerivedContentItem;
      if (dmContentIsAssetItem(item)) {
        assetItems.push(item);
        contentItem = dmCreateMediaContentItem(item.name, '', item.mediaType);
      } else {
        invalidContentItem = dmContentItemIsMediaContentItem(item);
        contentItem = item;
        assetItems.push(undefined);
      }
      if (contentData) {
        const contentItemData = dmFilterContentItemData(contentItem.type, contentData[contentIndex]);
        if (contentItemData) {
          contentItem = Object.assign({}, contentItem, contentItemData);
        }
      }
      contentItems.push(contentItem);
    });
    // If any given content is a contentItem, reject if it is a MediaContentItem
    if (invalidContentItem) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'MediaContentItems must be specified by AssetItem parameters', mainAction);
    }
    const zoneType = dmGetZoneTypeById(dmState, {id: container.id});
    let contentItemsInvalid = false;
    contentItems.forEach((item) => {
      if (!dmIsValidContentItemForPlaylist(item, zoneType)) {
        contentItemsInvalid = true;
      }
    });
    if (contentItemsInvalid) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'One or more content item types cannot be added to this Zone type', mainAction);
    }
    // Append mediaStates if index in undefined or invalid
    if (index === undefined || index > playlistIds.length || index < 0) {
      index = playlistIds.length;
    }

    // Get IDs of previous and next mediaStates in the playlist (around the insertion point)
    let prevMediaStateId: BsDmId;
    let nextMediaStateId: BsDmId;
    if (playlistIds.length > 1) {
      if (index > 0) {
        prevMediaStateId = playlistIds[index - 1];
      } else {
        prevMediaStateId = playlistIds[playlistIds.length - 1];
      }
      if (index === playlistIds.length) {
        nextMediaStateId = playlistIds[0];
      } else {
        nextMediaStateId = playlistIds[index];
      }
    } else if (playlistIds.length === 1) {
      prevMediaStateId = nextMediaStateId = playlistIds[0];
    }

    // Currently, default TransitionType does not depend on contentItem
    // If that changes, we will need to take a different approach, because we currently add transitions after
    //  all MediaStates are added.
    const transitionType = getDefaultTransitionType();

    const batchedActions: BsDmBaseAction[] = [];
    const addedEvents: BsDmId[] = [];
    contentItems.forEach((item, itemIndex) => {
      // Get defaults for this contentItem
      const eventType = getDefaultEventTypeForContentItem(item);
      const eventData = getDefaultEventDataForEventType(eventType, item);

      // If the contentItem has a corresponding assetItem, we need to put the assetItem to the AssetMap
      if (assetItems[itemIndex]) {
        // Duplicate the asset item, since we are going to mutate it
        let assetItem = Object.assign({}, assetItems[itemIndex]);
        assetItem = dmSetAssetId(dmState, assetItem);
        (item as DmMediaContentItem).assetId = assetItem.id;
        batchedActions.push(dmPutAssetItemWithoutTest(assetItem));
      }
      mainAction.payload.contentItems.push(item);

      let mediaStateName = getName(itemIndex);
      if (mediaStateName === '') {
        mediaStateName = getDefaultNameForContentItem(item, playlistIds.length + itemIndex + 1);
      }
      const addMediaStateAction = dmAddMediaStateWithContentItem(mediaStateName, container, item);
      // Save media state ID in the 'id' array in the action to be returned
      mainAction.payload.id.push(addMediaStateAction.payload.id);
      mainAction.payload.name.push(mediaStateName);
      batchedActions.push(addMediaStateAction);

      // Add event for media state
      const eventAction = dmAddEvent(name + '_ev', eventType, addMediaStateAction.payload.id, eventData);
      // Save the eventId - we will need it to add transitions later
      addedEvents.push(eventAction.payload.id);
      batchedActions.push(eventAction);
    });

    // Adjust the transition target for prevMediaState to the first mediaState added
    if (prevMediaStateId) {
      const prevMediaState = dmGetMediaStateById(dmState, {id: prevMediaStateId});
      if (prevMediaState) {
        const transitionId = prevMediaState.eventList[0].defaultTransition.id;
        batchedActions.push(dmUpdateTransition({
          id: transitionId,
          targetMediaStateId: mainAction.payload.id[0],
        }));
      }
    }
    // Add transitions for all new MediaState events, pointing to the following MediaState
    addedEvents.forEach((eventId, itemIndex) => {
      const nextStateId = itemIndex + 1 < mainAction.payload.id.length ?
        mainAction.payload.id[itemIndex + 1] :
        (nextMediaStateId ? nextMediaStateId : mainAction.payload.id[0]);
      batchedActions.push(
        dmAddTransition(mainAction.payload.name[itemIndex] + '_tr', eventId, nextStateId, transitionType));
    });

    if (index === 0) {
      // If new states are placed at the front of the list, update initialMediaStateId for the zone
      // Use our internal function that doesn't use the thunk to test initialMediaState
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: mainAction.payload.id[0]}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

/**
 * Update a mediaState with new parameters for a mediaStates in a playlist.
 * A special thunked action is needed for this, to validate the contentItem type. A BsDmError will be thrown if
 *  the contentItem is not valid for the zone.
 *
 * @param params {MediaStateParams} - mediaState parameters to update
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return a MediaStateAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistUpdateMediaState(params: MediaStateParams): BsDmThunkAction<MediaStateParams> {
  return (dispatch, getState): MediaStateAction  => {
    const updateMediaStateAction = dmUpdateMediaState(params);

    const dmState = dmFilterDmState(getState());
    const mediaState = dmGetMediaStateStateById(dmState, {id: params.id});
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: mediaState.container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', updateMediaStateAction);
    }
    if (params.hasOwnProperty('contentItem')) {
      const zoneType = dmGetZoneTypeById(dmState, {id: mediaState.container.id});
      if (!dmIsValidContentItemForPlaylist(params.contentItem, zoneType)) {
        throw new BsDmError(DmErrorType.invalidParameters,
          'Content item type "' + params.contentItem.type + '" cannot be added to "' + zoneType + '" zone',
          updateMediaStateAction);
      }
    }
    dispatch(updateMediaStateAction);
    return updateMediaStateAction;
  };
}

export interface PlaylistDeleteMediaStateRangeParams {
  containerId: BsDmId;
  startingIndex: number;
  count?: number;
}
export type PlaylistDeleteMediaStateRangeAction = BsDmAction<PlaylistDeleteMediaStateRangeParams>;

/**
 * Delete one or more MediaStates from a playlist.
 *  If deleting multiple states, this function deletes a range of consecutive media states
 *  This function will fail if the MediaState container does not contain a valid simple playlist.
 *  This function will fail if startingIndex and count denote an invalid range within the playlist.
 *
 * @param container {DmMediaStateContainer} - MediaState container
 * @param startingIndex {number} - zero based index of first MediaState to delete
 * @param [count=1] {number} - total number of MediaStates to delete
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return
 *   a PlaylistDeleteMediaStateRangeAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistDeleteMediaStateRange(
  container: DmMediaStateContainer,
  startingIndex: number,
  count?: number,
): BsDmThunkAction<PlaylistDeleteMediaStateRangeParams> {
  return (dispatch, getState): PlaylistDeleteMediaStateRangeAction  => {
    const mainAction: PlaylistDeleteMediaStateRangeAction =  {
      type: PLAYLIST_DELETE_MEDIA_STATE_RANGE,
      payload: { containerId: container.id, startingIndex, count },
    };
    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }

    if (count === undefined) {
      count = 1;
    }
    if (startingIndex >= playlistIds.length || count === 0 || startingIndex + count > playlistIds.length) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Invalid parameters for deletion range', mainAction);
    }

    const endingIndex = startingIndex + count;
    const mediaStateIdsToDelete: BsDmId[] = playlistIds.slice(startingIndex, endingIndex);

    // Get IDs of previous and next mediaStates in the playlist (around states to delete)
    let prevMediaStateId: BsDmId;
    let nextMediaStateId: BsDmId;
    const remainingMediaStates = playlistIds.length - count;
    if (remainingMediaStates > 1) {
      if (startingIndex > 0) {
        prevMediaStateId = playlistIds[startingIndex - 1];
      } else {
        prevMediaStateId = playlistIds[playlistIds.length - 1];
      }
      if (endingIndex === playlistIds.length) {
        nextMediaStateId = playlistIds[0];
      } else {
        nextMediaStateId = playlistIds[endingIndex];
      }
    } else if (remainingMediaStates === 1) {
      if (startingIndex > 0) {
        prevMediaStateId = nextMediaStateId = playlistIds[0];
      } else {
        prevMediaStateId = nextMediaStateId = playlistIds[playlistIds.length - 1];
      }
    }

    // Get the media states for which we need to adjust transitions
    let prevMediaState;
    if (prevMediaStateId) {
      prevMediaState = dmGetMediaStateById(dmState, {id: prevMediaStateId});
    }

    // Get a list of assetItems associated with the removed media states
    const assetIds: BsAssetId[] = [];
    mediaStateIdsToDelete.forEach((id) => {
      const mediaStateState = dmGetMediaStateStateById(dmState, {id});
      if (mediaStateState && dmContentItemIsMediaContentItem(mediaStateState.contentItem)) {
        assetIds.push(mediaStateState.contentItem.assetId);
      }
    });

    const batchedActions: BsDmBaseAction[] = [];
    // Remove references to any assetItems
    if (assetIds.length > 0) {
      batchedActions.push(dmRemoveAssetItems(assetIds));
    }
    // Get mediaState deletion params (includes all associated events and transitions)
    const deleteParams = dmGetMediaStateDeleteParams(dmState, mediaStateIdsToDelete);
    batchedActions.push(dmDeleteMediaStateWithParams(deleteParams));

    // Reset transition targets for the media state before the deleted states
    if (prevMediaState) {
      const transitionId = prevMediaState.eventList[0].defaultTransition.id;
      batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId: nextMediaStateId}));
    }

    if (startingIndex === 0) {
      // Reset initialMediaStateForZone if the first mediaState was deleted
      const newInitialMediaState = remainingMediaStates > 0 ? nextMediaStateId : BsDmIdNone;
      // Use our internal function that doesn't use the thunk to test initialMediaState
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: newInitialMediaState}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

export interface PlaylistDeleteMediaStateIndicesParams {
  containerId: BsDmId;
  indices: number[];
}
export type PlaylistDeleteMediaStateIndicesAction = BsDmAction<PlaylistDeleteMediaStateIndicesParams>;

/**
 * Delete one or more MediaStates from a playlist.
 *  This function deletes all mediaStates at the indices in the given index array
 *  This function will fail if the MediaState container does not contain a valid simple playlist.
 *  This function will fail if indices array is empty or undefined.
 *  The function will NOT fail if the arrays contains invalid (out of range) indices,
 *   but any such invalid indices will be ignored.
 *
 * @param container {DmMediaStateContainer} - MediaState container
 * @param indices {number[]} - array of indices (integers) corresponding to playlist mediaState positions
 *  at which to delete mediaStates
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return
 *   a PlaylistDeleteMediaStateIndicesAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistDeleteMediaStatesAtIndices(
  container: DmMediaStateContainer,
  indices: number[],
): BsDmThunkAction<PlaylistDeleteMediaStateIndicesParams> {
  return (dispatch, getState): PlaylistDeleteMediaStateIndicesAction  => {
    const mainAction: PlaylistDeleteMediaStateIndicesAction = {
      type: PLAYLIST_DELETE_MEDIA_STATE_AT_INDICES,
      payload: {containerId: container.id, indices},
    };
    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }

    if (!indices || indices.length === 0) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Incorrect parameters for media state delete by indices operation', mainAction);
    }

    indices.sort((a: number, b: number) => a - b);
    const tempIndices = indices.slice();

    const finalPlaylistIds: BsDmId[] = [];
    const mediaStateIdsToDelete: BsDmId[] = [];
    const mediaStateIdsForTransitionsToUpdate: BsDmId[] = [];
    playlistIds.forEach((id, index) => {
      if (index === tempIndices[0]) {
        mediaStateIdsToDelete.push(id);
        const prevMediaState =
          finalPlaylistIds.length > 0 ? finalPlaylistIds[finalPlaylistIds.length - 1] : undefined;
        if (prevMediaState && mediaStateIdsForTransitionsToUpdate.indexOf(prevMediaState) < 0) {
          mediaStateIdsForTransitionsToUpdate.push(prevMediaState);
        }
        tempIndices.shift();
      } else {
        finalPlaylistIds.push(id);
      }
    });
    if (indices[0] === 0 && finalPlaylistIds.length > 0) {
      const finalMediaState = finalPlaylistIds[finalPlaylistIds.length - 1];
      if (finalMediaState && mediaStateIdsForTransitionsToUpdate.indexOf(finalMediaState) < 0) {
        mediaStateIdsForTransitionsToUpdate.push(finalMediaState);
      }
    }

    // Get a list of assetItems associated with the removed media states
    const assetIds: BsAssetId[] = [];
    mediaStateIdsToDelete.forEach((id) => {
      const mediaStateState = dmGetMediaStateStateById(dmState, {id});
      if (mediaStateState && dmContentItemIsMediaContentItem(mediaStateState.contentItem)) {
        assetIds.push(mediaStateState.contentItem.assetId);
      }
    });

    const batchedActions: BsDmBaseAction[] = [];
    // Remove references to any assetItems
    if (assetIds.length > 0) {
      batchedActions.push(dmRemoveAssetItems(assetIds));
    }
    // Get mediaState deletion params (includes all associated events and transitions)
    const deleteParams = dmGetMediaStateDeleteParams(dmState, mediaStateIdsToDelete);
    batchedActions.push(dmDeleteMediaStateWithParams(deleteParams));

    // Retarget all mediaState transitions preceding deleted mediaStates
    finalPlaylistIds.forEach((id, index) => {
      const mediaState = dmGetMediaStateById(dmState, {id});
      const targetMediaStateId =
        index + 1 < finalPlaylistIds.length ? finalPlaylistIds[index + 1] : finalPlaylistIds[0];
      if (mediaState) {
        const transitionId = mediaState.eventList[0].defaultTransition.id;
        batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId}));
      }
    });

    if (indices[0] === 0) {
      // Reset initialMediaStateForZone if the first mediaState was deleted
      const newInitialMediaState = finalPlaylistIds.length > 0 ? finalPlaylistIds[0] : BsDmIdNone;
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: newInitialMediaState}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

export interface PlaylistMoveMediaStateRangeParams {
  containerId: BsDmId;
  startingIndex: number;
  targetIndex: number;
  count?: number;
}
export type PlaylistMoveMediaStateRangeAction = BsDmAction<PlaylistMoveMediaStateRangeParams>;

/**
 * Move a contiguous range of one or more MediaStates within a playlist.
 * This function will fail if the MediaState container does not contain a valid simple playlist.
 * This function will fail if startingIndex, targetIndex, and count do not describe a valid move.
 *
 * @param container {DmMediaStateContainer} - MediaState container
 * @param startingIndex {number} - zero based index of first MediaState to move
 * @param targetIndex {number} - The index in the playlist at which the first mediaState to move
 *  should be located after the move.
 * @param [count=1] {number} - total number of MediaStates to move
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return
 *   a PlaylistMoveMediaStateRangeAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistMoveMediaStateRange(
  container: DmMediaStateContainer,
  startingIndex: number,
  targetIndex: number,
  count?: number,
): BsDmThunkAction<PlaylistMoveMediaStateRangeParams> {
  return (dispatch, getState): PlaylistMoveMediaStateRangeAction  => {
    const mainAction: PlaylistMoveMediaStateRangeAction =  {
      type: PLAYLIST_MOVE_MEDIA_STATE_RANGE_TO_INDEX,
      payload: { containerId: container.id, startingIndex, targetIndex, count },
    };
    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }

    if (count === undefined) {
      count = 1;
    }
    if (startingIndex >= playlistIds.length
      || count === 0
      || startingIndex === targetIndex
      || targetIndex + count > playlistIds.length) {

      throw new BsDmError(DmErrorType.invalidParameters,
        'Incorrect parameters for media state move operation', mainAction);
    }

    const endingIndex = startingIndex + count;
    // Make copy of array
    const intermediateListIds = playlistIds.slice();
    // Remove mediaStates that are moving
    intermediateListIds.splice(startingIndex, count);
    // Now intermediateListIds holds the non-moving list IDs

    // Get IDs of previous and next mediaStates in the playlist (around source states to move)
    // If we are moving all states but one, we don't have to reconnect non-moving states,
    //  so leave these IDs undefined.
    let prevMediaStateIdForSrc: BsDmId;
    let nextMediaStateIdForSrc: BsDmId;
    if (intermediateListIds.length > 1) {
      if (startingIndex > 0) {
        prevMediaStateIdForSrc = playlistIds[startingIndex - 1];
      } else {
        prevMediaStateIdForSrc = playlistIds[playlistIds.length - 1];
      }
      if (endingIndex === playlistIds.length) {
        nextMediaStateIdForSrc = playlistIds[0];
      } else {
        nextMediaStateIdForSrc = playlistIds[endingIndex];
      }
    }
    // Get IDs of the previous and next media states around the target insertion point
    //  based on a list in which the source events have been removed
    let prevMediaStateIdForTarget: BsDmId;
    let nextMediaStateIdForTarget: BsDmId;
    if (targetIndex >= intermediateListIds.length || targetIndex === 0) {
      prevMediaStateIdForTarget = intermediateListIds[intermediateListIds.length - 1];
      nextMediaStateIdForTarget = intermediateListIds[0];
    } else {
      prevMediaStateIdForTarget = intermediateListIds[targetIndex - 1];
      nextMediaStateIdForTarget = intermediateListIds[targetIndex];
    }
    // Get the IDs of first and last mediaStates to move
    const firstMediaStateIdToMove = playlistIds[startingIndex];
    const lastMediaStateIdToMove = playlistIds[endingIndex - 1];

    // Get the media states for which we need to adjust transitions
    let prevMediaStateForSrc;
    if (prevMediaStateIdForSrc) {
      prevMediaStateForSrc = dmGetMediaStateById(dmState, {id: prevMediaStateIdForSrc});
    }
    let prevMediaStateForTarget;
    if (prevMediaStateIdForTarget) {
      prevMediaStateForTarget = dmGetMediaStateById(dmState, {id: prevMediaStateIdForTarget});
    }
    let lastMediaStateToMove;
    if (lastMediaStateIdToMove) {
      lastMediaStateToMove = dmGetMediaStateById(dmState, {id: lastMediaStateIdToMove});
    }

    const batchedActions: BsDmBaseAction[] = [];
    // Reset transition targets for the media state preceding the removed states
    if (prevMediaStateForSrc) {
      const transitionId = prevMediaStateForSrc.eventList[0].defaultTransition.id;
      batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId: nextMediaStateIdForSrc}));
    }
    if (prevMediaStateForTarget) {
      const transitionId = prevMediaStateForTarget.eventList[0].defaultTransition.id;
      batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId: firstMediaStateIdToMove}));
    }
    if (lastMediaStateToMove) {
      const transitionId = lastMediaStateToMove.eventList[0].defaultTransition.id;
      batchedActions.push(dmUpdateTransition({id: transitionId, targetMediaStateId: nextMediaStateIdForTarget}));
    }

    if (startingIndex === 0 || targetIndex === 0) {
      // Reset initialMediaStateForZone if the first mediaState was changed
      const newInitialMediaStateId = targetIndex === 0 ? firstMediaStateIdToMove : intermediateListIds[0];
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: newInitialMediaStateId}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}

export interface PlaylistMoveMediaStateIndicesParams {
  containerId: BsDmId;
  indices: number[];
  targetIndex: number;
}
export type PlaylistMoveMediaStateIndicesAction = BsDmAction<PlaylistMoveMediaStateIndicesParams>;

/**
 * Move a non-contiguous set of MediaStates within a playlist.
 * This function will fail if the MediaState container does not contain a valid simple playlist.
 * This function will fail if the indices array is empty.
 *
 * The selected MediaStates will be removed from the playlist and then re-inserted
 *  as a contiguous group before the targetIndex. The targetIndex is the
 *  insertion index relative to the original playlist (i.e., before the mediaStates
 *  being removed are extracted.)
 *
 * @param container {DmMediaStateContainer} - MediaState container
 * @param indices {number[]} - array of zero-based indices (integers) corresponding to playlist mediaState positions
 *  of mediaStates to move
 * @param targetIndex {number} - The index in the original playlist before which the moved mediaStates
 *  should be re-inserted
 * @returns {BsDmThunkAction} When dispatched, the thunk function will return
 *   a PlaylistMoveMediaStateIndicesAction object.
 *   If the thunk function succeeds, the action properties returned will be the ones used.
 *   If the function fails, a BsDmError is thrown.
 */
export function dmPlaylistMoveMediaStatesAtIndices(
  container: DmMediaStateContainer,
  indices: number[],
  targetIndex: number,
): BsDmThunkAction<PlaylistMoveMediaStateIndicesParams> {
  return (dispatch, getState): PlaylistMoveMediaStateIndicesAction  => {
    const mainAction: PlaylistMoveMediaStateIndicesAction =  {
      type: PLAYLIST_MOVE_MEDIA_STATE_INDICES_TO_INDEX,
      payload: { containerId: container.id, indices, targetIndex },
    };
    const dmState = dmFilterDmState(getState());
    const playlistIds = dmGetZoneSimplePlaylist(dmState, {id: container.id});
    if (!playlistIds) {
      throw new BsDmError(DmErrorType.invalidOperation,
        'Zone does not contain a valid simple playlist', mainAction);
    }

    if (!indices || indices.length === 0) {
      throw new BsDmError(DmErrorType.invalidParameters,
        'Incorrect parameters for media state move by indices operation', mainAction);
    }

    if (targetIndex > playlistIds.length) {
      targetIndex = playlistIds.length;
    }

    indices.sort((a: number, b: number) => a - b);
    const tempIndices = indices.slice();

    const finalPlaylistIds: BsDmId[] = [];
    const movedMediaStateIds: BsDmId[] = [];
    const mediaStateIdsForTransitionsToUpdate: BsDmId[] = [];
    let finalTargetIndex = playlistIds.length;
    playlistIds.forEach((id, index) => {
      if (index === tempIndices[0]) {
        movedMediaStateIds.push(id);
        // Reset transition for all moved states
        mediaStateIdsForTransitionsToUpdate.push(id);
        const prevMediaState =
          finalPlaylistIds.length > 0 ? finalPlaylistIds[finalPlaylistIds.length - 1] : undefined;
        if (prevMediaState && mediaStateIdsForTransitionsToUpdate.indexOf(prevMediaState) < 0) {
          mediaStateIdsForTransitionsToUpdate.push(prevMediaState);
        }
        if (index === targetIndex) {
          // Can't insert before a moved state, so increment the target index
          ++targetIndex;
        }
        tempIndices.shift();
      } else {
        finalPlaylistIds.push(id);
        if (index === targetIndex) {
          finalTargetIndex = finalPlaylistIds.length - 1;
        }
      }
    });
    mainAction.payload.targetIndex = targetIndex;

    // We need to adjust the transition target of the state before the insertion point
    const preInsertMediaState = finalTargetIndex > 0 ?
      finalPlaylistIds[finalTargetIndex - 1] : finalPlaylistIds[finalPlaylistIds.length - 1];
    if (preInsertMediaState && mediaStateIdsForTransitionsToUpdate.indexOf(preInsertMediaState) < 0) {
      mediaStateIdsForTransitionsToUpdate.push(preInsertMediaState);
    }
    // We need to adjust transition target of last state if first state moved, or if target is at the
    //  end of the list
    if (indices[0] === 0 || targetIndex === playlistIds.length) {
      const finalMediaState = finalPlaylistIds[finalPlaylistIds.length - 1];
      if (finalMediaState && mediaStateIdsForTransitionsToUpdate.indexOf(finalMediaState) < 0) {
        mediaStateIdsForTransitionsToUpdate.push(finalMediaState);
      }
    }

    // Insert moved IDs into finalPlaylistIds array
    finalPlaylistIds.splice(finalTargetIndex, 0, ...movedMediaStateIds);

    const batchedActions: BsDmBaseAction[] = [];
    // Retarget all affected mediaState transitions
    finalPlaylistIds.forEach((id, index) => {
      const transitionChangeIndex = mediaStateIdsForTransitionsToUpdate.indexOf(id);
      if (transitionChangeIndex >= 0) {
        const mediaState = dmGetMediaStateById(dmState, {id});
        const targetMediaStateId =
          index + 1 < finalPlaylistIds.length ? finalPlaylistIds[index + 1] : finalPlaylistIds[0];
        if (mediaState) {
          const transitionId = mediaState.eventList[0].defaultTransition.id;
          batchedActions.push(dmUpdateTransition({
            id: transitionId,
            targetMediaStateId,
          }));
        }
        mediaStateIdsForTransitionsToUpdate.splice(transitionChangeIndex, 1);
      }
    });

    if (indices[0] === 0 || targetIndex === 0) {
      // Reset initialMediaStateForZone if the first mediaState was moved
      const newInitialMediaState = finalPlaylistIds.length > 0 ? finalPlaylistIds[0] : BsDmIdNone;
      batchedActions.push(dmUpdateZoneWithoutTest({id: container.id, initialMediaStateId: newInitialMediaState}));
    }
    dispatch(dmBatchActions(batchedActions));
    return mainAction;
  };
}



