import {Action, Dispatch, ActionCreator} from 'redux';
import {DmState} from '../reducers/index';

// Dispatch type for bsDataModel actions
export type DmDispatch = Dispatch<DmState>;

export interface BsDmBaseAction extends Action {
  type: string;   // override Any - must be a string
  payload: {};
  error?: boolean;
  meta?: {};
}

export interface BsDmAction<T> extends BsDmBaseAction {
  payload: T;     // override payload with specific parameter type
}

// Create action based on payload object (params)
export type BsDmActionCreator<T> = ActionCreator<BsDmAction<T>>;

// BsDm specific thunk function
// Dispatch is always a DmDispatch
// getState always returns DmState
// Thunk function itself always returns the original (or composed) BsDmAction
// The function parameters match redux-thunk ThunkFunction parameters
export type BsDmThunkAction<T> = (
  dispatch: DmDispatch,
  getState: () => DmState,
  extraArgument: undefined,
) => BsDmAction<T>;

// Support for batched actions, as done in the redux-batched-actions package
/** @internal */
/** @private */
export const BSDM_BATCH = 'BSDM_BATCH';

/** @internal */
/** @private */
export function dmBatchActions(actions: BsDmBaseAction[]): BsDmBatchAction {
  return {type: BSDM_BATCH, payload: actions};
}

export interface BsDmBatchAction extends Action {
  type: string;   // override Any - must be a string
  payload: BsDmBaseAction[];
}

/**
 * Get DmState object from a enclosing parent state.
 * This function depends on the assumption that:
 *   1. If the parent state contains a DmState object, the property name for that object will
 *      be either 'bsdm' or 'bsdatamodel'.
 *   2. If the parent state does not contain a property with either of these names,
 *      the parent state is itself a DmState.
 *
 * @param state {any} - parent state
 * @returns {DmState} - returned DmState object
 */
export function dmFilterDmState(state: any): DmState {
  if (state.hasOwnProperty('bsdm')) {
    return state.bsdm as DmState;
  } else if (state.hasOwnProperty('bsdatamodel')) {
    return state.bsdatamodel as DmState;
  }
  return state as DmState;
}



