import * as fse from 'fs-extra';
import * as path from 'path';
import {
  BsAssetItem, AssetLocation, AssetType, BscFileTypeInfo,
  bscGenerateAssetLocator, bscGetBscFileTypeInfo,
} from '@brightsign/bscore';
import {FsErrorType, FsError} from './error';

export function getLocalJsonFileAsObject(fullPath: string): Promise<object> {
  if (!fse.existsSync(fullPath)) {
    return Promise.reject(new FsError(FsErrorType.fileNotFoundError, fullPath));
  }
  return fse.readFile(fullPath, 'utf8')
    .then((str) => JSON.parse(str));
}

export function saveObjectAsLocalJsonFile(data: object, fullPath: string): Promise<void> {
  const jsonString = JSON.stringify(data, null, 2);
  return fse.writeFile(fullPath, jsonString);
}

export function localFileExists(fullPath: string): boolean {
  return fse.existsSync(fullPath);
}

export function deleteFile(fullPath: string): Promise<void> {
  return fse.unlink(fullPath);
}

export function joinPath(dirPath: string, fileName: string) {
  return path.join(dirPath, fileName);
}

/** @internal */
/** @private */
export function getBscFileTypeInfo(name: string, stats: fse.Stats): BscFileTypeInfo {
  return stats.isDirectory() ?
    {assetType: AssetType.Folder} : bscGetBscFileTypeInfo(name);
}

/** @internal */
/** @private */
export function getAssetItemFromFileStats(name: string, dirPath: string, stats: fse.Stats): BsAssetItem {
  // Make sure path has trailing separator
  if (dirPath.charAt(dirPath.length - 1) !== path.sep) {
    dirPath = dirPath + path.sep;
  }
  const typeInfo = getBscFileTypeInfo(name, stats);
  const assetItem: BsAssetItem = {
    id: '',
    name,
    path: dirPath,
    networkId: 0,
    location: AssetLocation.Local,
    locator: '',
    assetType: typeInfo.assetType,
    fileSize: stats.size,
    lastModifiedDate: stats.mtime,
    creationDate: stats.birthtime,
  };
  if (assetItem.assetType === AssetType.Content) {
    assetItem.mediaType = typeInfo.mediaType;
  }
  assetItem.locator = bscGenerateAssetLocator(assetItem);
  return assetItem;
}

export function getAssetItemFromFile(fullPath: string): BsAssetItem | null {
  try {
    const stats = fse.statSync(fullPath);
    return getAssetItemFromFileStats(path.basename(fullPath), path.dirname(fullPath), stats);
  } catch (error) {
    return null;
  }
}



