import { createSelector } from 'reselect';
import { pick, omit } from 'lodash';

import { HtmlSiteType } from '@brightsign/bscore';
import { BsDmId, DmHtmlSite, DmIdParam, DmNameParam } from '../bsDmInterfaces';
import { DmcHtmlSite } from '../bsDmClasses';
import {
  HtmlSiteAction, HtmlSiteLocalParams, HtmlSiteRemoteParams, HtmlSiteBrightPlateParams, HtmlSiteParams,
  ADD_HTML_SITE, UPDATE_HTML_SITE, DELETE_HTML_SITE,
} from '../actions/actionHtmlSite';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { BsDmAction } from '../actions/actionBase';
import { DmMap } from './index';

export type DmHtmlSiteMap = DmMap<DmHtmlSite> | {};

const getHtmlSiteForType = (payload: HtmlSiteParams) => {
  const {id, name, type, queryString} = payload as HtmlSiteParams;
  const baseSite = {id, name, type, queryString};
  switch (type) {
    case HtmlSiteType.Local: {
      const {indexAssetId} = payload as HtmlSiteLocalParams;
      return Object.assign(baseSite, {indexAssetId});
    }
    case HtmlSiteType.Remote: {
      const {url} = payload as HtmlSiteRemoteParams;
      return Object.assign(baseSite, {url});
    }
    case HtmlSiteType.BrightPlate: {
      const {brightPlateName, displayName} = payload as HtmlSiteBrightPlateParams;
      return Object.assign(baseSite, {brightPlateName, displayName});
    }
  }
  return undefined;
};

const getUpdateHtmlParamsForType = (payload: HtmlSiteParams) => {
  const {type} = payload;
  const updateParams = pick(payload, ['type', 'name', 'queryString']);
  switch (type) {
    case HtmlSiteType.Local: {
      const {indexAssetId} = payload as HtmlSiteLocalParams;
      return Object.assign(updateParams as HtmlSiteLocalParams, pick(payload, 'indexAssetId'));
    }
    case HtmlSiteType.Remote: {
      const {url} = payload as HtmlSiteRemoteParams;
      return Object.assign(updateParams as HtmlSiteRemoteParams, pick(payload, 'url'));
    }
    case HtmlSiteType.BrightPlate: {
      const {brightPlateName, displayName} = payload as HtmlSiteBrightPlateParams;
      return Object.assign(updateParams as HtmlSiteBrightPlateParams,
        pick(payload, ['brightPlateName', 'displayName']));
    }
  }
  return undefined;
};

const htmlSitesById = (
  state: DmHtmlSiteMap = {},
  action: (HtmlSiteAction | OpenSignAction),
): DmHtmlSiteMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      // if undefined in given sign, create empty object
      return newState.htmlSites ? newState.htmlSites : {};
    case ADD_HTML_SITE: {
      const {id} = payload as HtmlSiteParams;
      const newSite = getHtmlSiteForType(payload as HtmlSiteParams);
      if (newSite) {
        return Object.assign({}, state, {[id]: newSite});
      }
      break;
    }
    case UPDATE_HTML_SITE: {
      const {id} = payload as HtmlSiteParams;
      // Make sure there is a site type in the params passed to getUpdateHtmlParamsForType
      if (!payload.hasOwnProperty('type')) {
        (payload as HtmlSiteParams).type = state[id].type;
      }
      const updateParams = getUpdateHtmlParamsForType(payload as HtmlSiteParams);
      if (updateParams) {
        // If type is changing, just replace the object with the new params
        const updatedHtmlSite = updateParams.type === state[id].type ?
          Object.assign({}, state[id], updateParams) :
          updateParams;
        return Object.assign({}, state, {[id]: updatedHtmlSite});
      }
      break;
    }
    case DELETE_HTML_SITE:
      return omit(state, (payload as HtmlSiteParams).id);
  }
  return state;
};

const htmlSiteReducer = htmlSitesById;

/** @internal */
/** @private */
export default htmlSiteReducer;

export type DmHtmlSiteCollectionState = DmHtmlSiteMap;

/** @internal */
/** @private */
export const isValidHtmlSiteState = (state: DmHtmlSiteCollectionState): boolean => {
  // TODO: check contained Html Sites for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getHtmlSiteById = (state: DmHtmlSiteCollectionState, props: DmIdParam): DmcHtmlSite | null => {
  return state[props.id] ? new DmcHtmlSite(state[props.id]) : null;
};

/** @internal */
/** @private */
export const getHtmlSiteIdsForSign = (state: DmHtmlSiteCollectionState) => Object.keys(state);

const getHtmlSitesById = (state: DmHtmlSiteCollectionState) => state;

/** @internal */
/** @private */
export const getHtmlSiteByName = createSelector<DmHtmlSiteCollectionState, DmcHtmlSite | null, DmHtmlSiteMap, string> (
  getHtmlSitesById,
  (_, props: DmNameParam) => props.name as BsDmId,
  (htmlSites, name) => {
    let htmlSite: DmHtmlSite;
    Object.keys(htmlSites).some((id): boolean => {
      if (htmlSites[id].name === name) {
        htmlSite = htmlSites[id];
        return true;
      }
    });
    return htmlSite ? new DmcHtmlSite(htmlSite) : null;
  },
);



