// region HTML, LiveVideo and DataFeed ContentItems
/**
 * Create a DmHtmlContentItem.
 * This ContentItemState encapsulates an HTML site, along with HtmlContentItem specific parameters.
 *
 * All optional parameters will be set to user default if omitted.
 *
 * @param name {string} - name for the ContentItem
 * @param siteId {BsDmId} - ID of the HTML site to indicate site from HtmlSite collection in the sign
 * @param [enableExternalData=<user> (false)] {boolean}
 * @param [enableMouseEvents=<user> (false)] {boolean} - set to true if site should respond to mouse events
 * @param [displayCursor=<user> (false)] {boolean} - set to true to display mouse cursor
 * @param [hwzOn=<user> (false)] {boolean} - set to true to use accelerated graphics hardware for video playback
 * @param [useUserStylesheet=<user> (false)] {boolean} - set to true if a custom style sheet document is included
 * @param [userStylesheetAssetId] {string} - assetId for the custom stylesheet to use,
 *  if useUserStylesheet is true
 * @param [customFonts] (DmFont[]} - array of fonts to be included with the Html content
 * @returns {DmHtmlContentItem}
 */
export function dmCreateHtmlContentItem(
  name: string,
  siteId: BsDmId,
  enableExternalData?: boolean,
  enableMouseEvents?: boolean,
  displayCursor?: boolean,
  hwzOn?: boolean,
  useUserStylesheet?: boolean,
  userStylesheetAssetId?: string,
  customFonts?: DmFont[],
): DmHtmlContentItem {
  const defaults = dmGetDefaultHtmlContentItemData();
  const contentItemData: DmHtmlContentItemData = {
    enableExternalData: (enableExternalData === undefined ? defaults.enableExternalData : enableExternalData),
    enableMouseEvents: (enableMouseEvents === undefined ? defaults.enableMouseEvents : enableMouseEvents),
    displayCursor: (displayCursor === undefined ? defaults.displayCursor : displayCursor),
    hwzOn: (hwzOn === undefined ? defaults.hwzOn : hwzOn),
    useUserStylesheet: (userStylesheetAssetId === undefined ? defaults.useUserStylesheet : useUserStylesheet),
  };
  if (contentItemData.useUserStylesheet) {
    if (userStylesheetAssetId === undefined) {
      userStylesheetAssetId = defaults.userStylesheetAssetId;
    }
    if (userStylesheetAssetId !== undefined) {
      contentItemData.userStylesheetAssetId = userStylesheetAssetId;
    } else {
      contentItemData.useUserStylesheet = false;
    }
  }
  // Currently we do not support default custom fonts
  if (customFonts !== undefined) {
    contentItemData.customFonts = customFonts;
  }
  return Object.assign({name, type: ContentItemType.Html, siteId}, contentItemData);
}

