// region ContentItems for Interactive Zones
const VideoOrImagesZoneContentTypeArray: ContentItemType[] = [
  ContentItemType.Video, ContentItemType.Image, ContentItemType.Audio,
  ContentItemType.Html, ContentItemType.MrssFeed, ContentItemType.LiveVideo,
  ContentItemType.VideoStream, ContentItemType.AudioStream,
];
const VideoOrImagesZoneContentTypes: Set<ContentItemType> = new Set(VideoOrImagesZoneContentTypeArray);

const VideoOnlyZoneContentTypeArray: ContentItemType[] = [
  ContentItemType.Video, ContentItemType.LiveVideo,
  ContentItemType.VideoStream, ContentItemType.AudioStream,
];
const VideoOnlyZoneContentTypes: Set<ContentItemType> = new Set(VideoOnlyZoneContentTypeArray);

const ImagesZoneContentTypeArray: ContentItemType[] = [
  ContentItemType.Image, ContentItemType.Html, ContentItemType.MrssFeed,
];
const ImagesZoneContentTypes: Set<ContentItemType> = new Set(ImagesZoneContentTypeArray);

const AudioZoneContentTypeArray: ContentItemType[] = [
  ContentItemType.Audio, ContentItemType.AudioStream,
];
const AudioZoneContentTypes: Set<ContentItemType> = new Set(AudioZoneContentTypeArray);

const TickerZoneContentTypeArray: ContentItemType[] = [
  ContentItemType.Text, ContentItemType.DataFeed,
];
const TickerZoneContentTypes: Set<ContentItemType> = new Set(TickerZoneContentTypeArray);

/**
 * Return true if given contentItem is valid for the given zone type.
 * This test assumes a fully Interactive zone, and is not limited to nonInteractive playlists.
 * For nonInteractive playlists,
 *  use {@link Helpers:ContentItems~dmIsValidContentItemForPlaylist|dmIsValidContentItemForPlaylist}.
 *
 * @param contentItem {DmDerivedContentItem} - content item object to be checked
 * @param zoneType {ZoneType} - type of zone into which the contentItem would be added
 * @returns {boolean} - true if contentItem is valid for zone
 */
export function dmIsValidContentItemForZone(contentItem: DmDerivedContentItem, zoneType: ZoneType): boolean {
  return dmIsValidContentItemTypeForZone(contentItem.type, zoneType);
}

/**
 * Return true if given contentItemType is valid for the given zone type.
 * This test assumes a fully Interactive zone, and is not limited to nonInteractive playlists.
 * For nonInteractive playlists,
 *  use {@link Helpers:ContentItems~dmIsValidContentItemTypeForPlaylist|dmIsValidContentItemTypeForPlaylist}.
 *
 * @param contentItemType {ContentItemType} - ContentItemType to be checked
 * @param zoneType {ZoneType} - type of zone into which the contentItem would be added
 * @returns {boolean} - true if contentItem is valid for zone
 */
export function dmIsValidContentItemTypeForZone(contentItemType: ContentItemType, zoneType: ZoneType): boolean {
  switch (zoneType) {
    case ZoneType.VideoOrImages:
      return VideoOrImagesZoneContentTypes.has(contentItemType);
    case ZoneType.VideoOnly:
      return VideoOnlyZoneContentTypes.has(contentItemType);
    case ZoneType.Images:
      return ImagesZoneContentTypes.has(contentItemType);
    case ZoneType.AudioOnly:
    case ZoneType.EnhancedAudio:
      return AudioZoneContentTypes.has(contentItemType);
    case ZoneType.Ticker:
      return TickerZoneContentTypes.has(contentItemType);
    case ZoneType.Clock:
      return false;
    case ZoneType.BackgroundImage:
      return contentItemType === ContentItemType.Image;
  }
  return false;
}
// endregion

// region Content Items for Non-Interactive Zones (Playlists)
const VideoOrImagesPlaylistContentTypeArray: ContentItemType[] = [
  ContentItemType.Video, ContentItemType.Image, ContentItemType.Audio,
  ContentItemType.Html, ContentItemType.MrssFeed, ContentItemType.LiveVideo,
  ContentItemType.VideoStream, ContentItemType.AudioStream,
];
const VideoOrImagesPlaylistContentTypes: Set<ContentItemType> = new Set(VideoOrImagesPlaylistContentTypeArray);

const VideoOnlyPlaylistContentTypeArray: ContentItemType[] = [
  ContentItemType.Video, ContentItemType.LiveVideo,
  ContentItemType.VideoStream, ContentItemType.AudioStream,
];
const VideoOnlyPlaylistContentTypes: Set<ContentItemType> = new Set(VideoOnlyPlaylistContentTypeArray);

const ImagesPlaylistContentTypeArray: ContentItemType[] = [
  ContentItemType.Image, ContentItemType.Html, ContentItemType.MrssFeed,
];
const ImagesPlaylistContentTypes: Set<ContentItemType> = new Set(ImagesPlaylistContentTypeArray);

const AudioPlaylistContentTypeArray: ContentItemType[] = [
  ContentItemType.Audio, ContentItemType.AudioStream,
];
const AudioPlaylistContentTypes: Set<ContentItemType> = new Set(AudioPlaylistContentTypeArray);

const TickerPlaylistContentTypeArray: ContentItemType[] = [
  ContentItemType.Text, ContentItemType.DataFeed,
];
const TickerPlaylistContentTypes: Set<ContentItemType> = new Set(TickerPlaylistContentTypeArray);

/**
 * Return true if given contentItem is valid for the given zone type.
 * This test assumes a nonInteractive zone.
 * For Interactive zones,
 *  use {@link Helpers:ContentItems~dmIsValidContentItemForZone|dmIsValidContentItemForZone}.
 *
 * @param contentItem {DmDerivedContentItem} - content item object to be checked
 * @param zoneType {ZoneType} - type of zone into which the contentItem would be added
 * @returns {boolean} - true if contentItem is valid for zone
 */
export function dmIsValidContentItemForPlaylist(contentItem: DmDerivedContentItem, zoneType: ZoneType): boolean {
  return dmIsValidContentItemTypeForPlaylist(contentItem.type, zoneType);
}

/**
 * Return true if given contentItem is valid for the given zone type.
 * This test assumes a nonInteractive zone.
 * For Interactive zones,
 *  use {@link Helpers:ContentItems~dmIsValidContentItemTypeForZone|dmIsValidContentItemTypeForZone}.
 *
 * @param contentItemType {ContentItemType} - content item type to be checked
 * @param zoneType {ZoneType} - type of zone into which the contentItem would be added
 * @returns {boolean} - true if contentItem is valid for zone
 */
export function dmIsValidContentItemTypeForPlaylist(contentItemType: ContentItemType, zoneType: ZoneType): boolean {
  switch (zoneType) {
    case ZoneType.VideoOrImages:
      return VideoOrImagesPlaylistContentTypes.has(contentItemType);
    case ZoneType.VideoOnly:
      return VideoOnlyPlaylistContentTypes.has(contentItemType);
    case ZoneType.Images:
      return ImagesPlaylistContentTypes.has(contentItemType);
    case ZoneType.AudioOnly:
    case ZoneType.EnhancedAudio:
      return AudioPlaylistContentTypes.has(contentItemType);
    case ZoneType.Ticker:
      return TickerPlaylistContentTypes.has(contentItemType);
    case ZoneType.Clock:
      return false;
    case ZoneType.BackgroundImage:
      return contentItemType === ContentItemType.Image;
  }
  return false;
}
// endregion

// region Manage ContentItemData
export function dmFilterContentItemData(
  contentItemType: ContentItemType,
  contentItemData: any,
): DmContentItemData | null {
  if (contentItemData) {
    switch (contentItemType) {
      case ContentItemType.Video:
        return pick(contentItemData, DmVideoContentItemDataNameArray) as DmVideoContentItemData;
      case ContentItemType.Image:
        return pick(contentItemData, DmImageContentItemDataNameArray) as DmImageContentItemData;
      case ContentItemType.Audio:
        return pick(contentItemData, DmAudioContentItemDataNameArray) as DmAudioContentItemData;
      case ContentItemType.Html:
        return pick(contentItemData, DmHtmlContentItemDataNameArray) as DmHtmlContentItemData;
      case ContentItemType.LiveVideo:
        return pick(contentItemData, DmLiveVideoContentItemDataNameArray) as DmLiveVideoContentItemData;
      case ContentItemType.MrssFeed:
        return pick(contentItemData, DmMrssDataFeedContentItemDataNameArray) as DmMrssDataFeedContentItemData;
      case ContentItemType.VideoStream:
      case ContentItemType.AudioStream:
        return pick(contentItemData, DmStreamContentItemDataNameArray) as DmStreamContentItemData;
    }
  }
  return null;
}
// endregion



