import {MediaType, BsAssetItem, BsImageData, RotationType} from '@brightsign/bscore';
import * as fse from 'fs-extra';
import * as path from 'path';

import * as ExifImage from 'exif';
import * as sizeOf from 'image-size';

/**
 * Get EXIF metadata for given Image asset.
 * @param assetItem - assetItem for which to return EXIF data - should be MediaType.Image
 * @returns {Promise<BsImageData>} - returns a Promise. For image files, promise resolves
 *  with ImageData object. For unsupported asset files, promise resolves with null.
 */
export function getExifData(assetItem: BsAssetItem): Promise<BsImageData | null> {

  const fullPath = path.join(assetItem.path, assetItem.name);
  if (assetItem.mediaType !== MediaType.Image || !fse.existsSync(fullPath)) {
    return Promise.resolve(null);
  }

  return new Promise<BsImageData>((resolve, reject) => {
    try {
      const ex = new ExifImage.ExifImage({ image : fullPath }, (error, exifData) => {
        const imageData: BsImageData = {
          size: {width: 0, height: 0},
          orientation: RotationType.rot0,
        };

        if (exifData
            && typeof exifData.exif.ExifImageWidth === 'number'
            && typeof exifData.exif.ExifImageHeight === 'number') {
          imageData.size.width = exifData.exif.ExifImageWidth;
          imageData.size.height = exifData.exif.ExifImageHeight;
        } else {
          const dimensions = sizeOf(fullPath);
          imageData.size.height = dimensions.height;
          imageData.size.width = dimensions.width;
        }

        if (exifData && typeof exifData.image.Orientation !== 'undefined') {
          switch (exifData.image.Orientation) {
            case 1:
            case 2: imageData.orientation = RotationType.rot0; break;
            case 3:
            case 4: imageData.orientation = RotationType.rot180; break;
            case 6:
            case 7: imageData.orientation = RotationType.rot90; break;
            case 5:
            case 8: imageData.orientation = RotationType.rot270; break;
          }
        }

        if (!exifData || typeof exifData.exif.DateTimeOriginal === 'undefined') {
          // if no EXIF dateTaken, get file creation time
          fse.stat(fullPath).then((stats) => {
            imageData.dateTaken = stats.birthtime;
            resolve(imageData);
          });
        } else {
          const res = exifData.exif.DateTimeOriginal.match(/(\d{4}):(\d{2}):(\d{2}) (\d{2}):(\d{2}):(\d{2})/);
          if (res != null && res.length === 7) {
            imageData.dateTaken = new Date(res[1], res[2] - 1, res[3], res[4], res[5], res[6]);
          }
          resolve(imageData);
        }
      });
    } catch (error) {
      reject(error);
    }
  });
}



