import { combineReducers, Action } from 'redux';
import { createSelector } from 'reselect';
import { omit, cloneDeep } from 'lodash';

import { VideoMode, ZoneType, BsRect } from '@brightsign/bscore';
import {
  BsDmId, BsDmIdNone,
  DmIdParam, DmNameParam, DmTagParam, DmTypeParam,
  DmZone, DmZoneTagMap, DmZoneSpecificProperties,
} from '../bsDmInterfaces';
import { DmState, DmMap } from './index';
import { DmcZone } from '../bsDmClasses';
import {
  dmGetDefaultAudioZoneProperties, dmGetDefaultEnhancedAudioZoneProperties, dmGetDefaultImageZoneProperties,
  dmGetDefaultVideoZoneProperties, dmGetDefaultTickerZoneProperties, dmGetDefaultClockZoneProperties,
} from '../bsDmDefaults';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import {
  ZoneParams, ZoneAction, ZonePropertyUpdateParams, ZonePropertyUpdateAction, ZoneDeleteParams, ZoneDeleteAction,
  ADD_ZONE, UPDATE_ZONE, UPDATE_ZONE_PROPS, DELETE_ZONE,
} from '../actions/actionZone';
import { MediaStateParams, MediaStateAction, ADD_MEDIA_STATE } from '../actions/actionMediaState';

export type DmZoneMap = DmMap<DmZone> | {};

// Return a simple object conforming to DmZone
/** @internal */
/** @private */
export const createDefaultZoneState = (
  id: BsDmId,
  name: string,
  type: ZoneType,
  tag: string,
  nonInteractive: boolean = true,
  position: BsRect = {x: 0, y: 0, width: 100, height: 100, pct: true},
): DmZone => ({
  id,
  name,
  type,
  tag,
  nonInteractive,
  initialMediaStateId: BsDmIdNone,
  position,
  properties: createDefaultZoneProperties(type),
});

/** @internal */
/** @private */
export const createDefaultZoneProperties = (type: ZoneType): DmZoneSpecificProperties => {
  switch (type) {
    case ZoneType.VideoOrImages:
      return Object.assign(
        dmGetDefaultVideoZoneProperties(),
        dmGetDefaultAudioZoneProperties(),
        dmGetDefaultImageZoneProperties());
    case ZoneType.VideoOnly:
      return Object.assign(dmGetDefaultVideoZoneProperties(), dmGetDefaultAudioZoneProperties());
    case ZoneType.Images:
      return dmGetDefaultImageZoneProperties();
    case ZoneType.AudioOnly:
      return dmGetDefaultAudioZoneProperties();
    case ZoneType.EnhancedAudio:
      return Object.assign(dmGetDefaultAudioZoneProperties(), dmGetDefaultEnhancedAudioZoneProperties());
    case ZoneType.Ticker:
      return dmGetDefaultTickerZoneProperties();
    case ZoneType.Clock:
      return dmGetDefaultClockZoneProperties();
  }
  return {};
};

const zonesById = (
  state: DmZoneMap = {},
  {type, payload}: (ZoneAction | ZonePropertyUpdateAction | ZoneDeleteAction | MediaStateAction | OpenSignAction),
): DmZoneMap => {
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.zones.zonesById;
    case ADD_ZONE: {
      const {id, name: zoneName, type: zoneType, tag, nonInteractive, position} = payload as ZoneParams;
      return Object.assign({}, state,
        {[id]: createDefaultZoneState(id, zoneName, zoneType, tag, nonInteractive, position)});
    }
    case UPDATE_ZONE: {
      const {id} = payload as ZoneParams;
      const updatedZone = Object.assign({}, state[id], payload);
      return Object.assign({}, state, {[id]: updatedZone});
    }
    case UPDATE_ZONE_PROPS: {
      const {id, properties} = payload as ZonePropertyUpdateParams;
      const updatedProperties = Object.assign({}, state[id].properties, properties);
      const updatedZone = Object.assign({}, state[id], {properties: updatedProperties});
      return Object.assign({}, state, {[id]: updatedZone});
    }
    case DELETE_ZONE: {
      const {zoneId} = payload as ZoneDeleteParams;
      return omit(state, zoneId);
    }
    case ADD_MEDIA_STATE:
      // First media state added to a zone always becomes the initial state
      // For this action, action.id == mediaStateId, mediaState.container.id == ZoneId
      const {id, container} = payload as MediaStateParams;
      const zone = state[container.id];
      if (zone && zone.initialMediaStateId === BsDmIdNone) {
        const updatedZone = Object.assign({}, state[container.id], {initialMediaStateId: id});
        return Object.assign({}, state, {[container.id]: updatedZone});
      }
      break;
  }
  return state;
};

const allZones = (
  state: BsDmId[] = [],
  {type, payload}: (ZoneAction | MediaStateAction | ZoneDeleteAction | OpenSignAction),
): BsDmId[] => {
  switch (type) {
    case NEW_SIGN:
      return [];
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.zones.allZones;
    case ADD_ZONE: {
      const {id} = payload as ZoneParams;
      return [...state, id];
    }
    case DELETE_ZONE: {
      const {zoneId} = payload as ZoneDeleteParams;
      const index = state.indexOf(zoneId);
      if (index >= 0) {
        return [...state.slice(0, index),
          ...state.slice(index + 1)];
      }
    }
  }
  return state;
};

const zoneReducer = combineReducers<DmZoneCollectionState>( {zonesById, allZones} );

/** @internal */
/** @private */
export default zoneReducer;

export interface DmZoneCollectionState {
  zonesById: DmZoneMap;
  allZones: BsDmId[];
}

/** @internal */
/** @private */
export const isValidZoneState = (state: DmZoneCollectionState): boolean => {
  return typeof state === 'object'
    && state.hasOwnProperty('zonesById')
    && typeof state.zonesById === 'object'
    && state.hasOwnProperty('allZones')
    && Array.isArray(state.allZones)
    && state.allZones.length === Object.keys(state.zonesById).length;
};

// Selectors

/** @internal */
/** @private */
export const getZoneById = (state: DmState, props: DmIdParam): DmcZone | null => {
  const zoneState = state.zones.zonesById[props.id];
  return zoneState ? new DmcZone(zoneState, state.sign.properties.videoMode) : null;
};

/** @internal */
/** @private */
export const getZoneByName = createSelector<DmState, DmcZone | null, DmZoneMap, string, VideoMode> (
  (state: DmState) => state.zones.zonesById,
  (_, props: DmNameParam) => props.name as string,
  (state: DmState) => state.sign.properties.videoMode,
  (zones, name, videoMode) => {
    let zoneState: DmZone;
    Object.keys(zones).some((id): boolean => {
      if (zones[id].name === name) {
        zoneState = zones[id];
        return true;
      }
    });
    return zoneState ? new DmcZone(zoneState, videoMode) : null;
  },
);

/** @internal */
/** @private */
export const getZoneByTag = createSelector<DmState, DmcZone | null, DmZoneMap, string, VideoMode> (
  (state: DmState) => state.zones.zonesById,
  (_, props: DmTagParam) => props.tag as string,
  (state: DmState) => state.sign.properties.videoMode,
  (zones, tag, videoMode) => {
    let zoneState: DmZone;
    Object.keys(zones).some((id): boolean => {
      if (zones[id].tag === tag) {
        zoneState = zones[id];
        return true;
      }
    });
    return zoneState ? new DmcZone(zoneState, videoMode) : null;
  },
);

/** @internal */
/** @private */
export const getZoneTypeById = (state: DmZoneCollectionState, props: DmIdParam): ZoneType | null => {
  const zoneState = state.zonesById[props.id];
  return zoneState ? zoneState.type : null;
};

/** @internal */
/** @private */
export const getZonePropertiesById = (
  state: DmZoneCollectionState,
  props: DmIdParam,
): DmZoneSpecificProperties | null => {
  const zoneState = state.zonesById[props.id];
  return zoneState ? cloneDeep(zoneState.properties) : null;
};

const getZonesById = (state: DmZoneCollectionState) => state.zonesById;

/** @internal */
/** @private */
export const getZonePropertiesByName = createSelector<
  DmZoneCollectionState, DmZoneSpecificProperties | null, DmZoneMap, string>
(
  getZonesById,
  (_, props: DmNameParam) => props.name as string,
  (zones, name) => {
    let zoneState: DmZone;
    Object.keys(zones).some((id): boolean => {
      if (zones[id].name === name) {
        zoneState = zones[id];
        return true;
      }
    });
    return zoneState ? cloneDeep(zoneState.properties) : null;
  },
);

/** @internal */
/** @private */
export const getZonePropertiesByTag = createSelector<
  DmZoneCollectionState, DmZoneSpecificProperties | null, DmZoneMap, string>
(
  getZonesById,
  (_, props: DmTagParam) => props.tag as string,
  (zones, tag) => {
    let zoneState: DmZone;
    Object.keys(zones).some((id): boolean => {
      if (zones[id].tag === tag) {
        zoneState = zones[id];
        return true;
      }
    });
    return zoneState ? cloneDeep(zoneState.properties) : null;
  },
);

/** @internal */
/** @private */
export const getZonesForSign = (state: DmZoneCollectionState) => state.allZones;
/** @internal */
/** @private */
export const getZoneCount = (state: DmZoneCollectionState) => state.allZones.length;

/** @internal */
/** @private */
export const getZoneIdsForZoneType = createSelector<DmZoneCollectionState, BsDmId[], DmZoneMap, ZoneType> (
  getZonesById,
  (_, props: DmTypeParam) => props.type as ZoneType,
  (zones, type) => {
    const zoneIds: BsDmId[] = [];
    Object.keys(zones).forEach((id) => {
      const zone = zones[id];
      if (zone.type === type) {
        zoneIds.push(id);
      }
    });
    return zoneIds;
  },
);

/** @internal */
/** @private */
export const getZoneTagMap = createSelector<DmZoneCollectionState, DmZoneTagMap, DmZoneMap> (
  getZonesById,
  (zones) => {
    const tagMap: DmZoneTagMap = {};
    Object.keys(zones).forEach((id) => {
      const zone = zones[id];
      if (zone.tag) {
        tagMap[zone.tag] = id;
      }
    });
    return tagMap;
  },
);



