/** @module Interfaces */
/**
 * Created by jimsugg on 5/1/16.
 */

import {
  VideoMode, PlayerModel, VideoConnectorType, MonitorOrientationType, MonitorOverscanType,  DeviceWebPageDisplay,
  TouchCursorDisplayModeType, GraphicsZOrderType, UdpAddressType,
  ZoneType, ViewModeType, ImageModeType, LiveVideoInputType, MosaicMaxContentResolutionType, LiveVideoStandardType,
  AudioOutputSelectionType, AudioModeType, AudioMappingType, AudioOutputType, AudioMixModeType,
  RotationType, TextHAlignmentType, TextScrollingMethodType,
  ContentItemType, VideoDisplayModeType, EventType, TransitionType,
  AccessType, HtmlSiteType, DataFeedType, DataFeedUsageType,
  StringParameterType, CompareOperator, LanguageType, LanguageKeyType, GpioType, BsAssetItem,
  BsColor, BsRect,
} from '@brightsign/bscore';

// region Base Definitions

/**
 * @type BsDmId {string} - a GUID string that identifies a data model object. These IDs are typically generated
 *  automatically when new objects are added.
 */
export type BsDmId = string;    // GUID string
export const BsDmIdNone = '0';
export const BsDmStateVersion = '0.6.1';

/**
 * @interface String component for use in DmParameterizedString
 * value: if type === 'Text', value = text string
 *        if type = UserVariable, value = BaDmId of UserVariable ID
 */
export interface DmStringComponent {
  type: StringParameterType;
  value: string;
}

export interface DmParameterizedString {
  params: DmStringComponent[];
}

export interface DmIdParam {
  id: BsDmId;
}

export interface DmNameParam {
  name: string;
}

export interface DmTagParam {
  tag: string;
}

export interface DmLocatorParam {
  locator: string;
}

// Type value will be a string enum value
export interface DmTypeParam {
  type: string;
}

export interface DmFont {
  name: string;
  assetId: string;
}

export interface DmObject {
  // Properties
  readonly id: BsDmId;
  readonly name: string;
}

// endregion

// region HtmlSites

export interface DmLocalHtmlSite extends DmObject {
  readonly type: 'Local';
  readonly queryString: DmParameterizedString;
  readonly indexAssetId: string;
}

export interface DmRemoteHtmlSite extends DmObject {
  readonly type: 'Remote';
  readonly queryString: DmParameterizedString;
  readonly url: string;
}

export interface DmBrightPlateHtmlSite extends DmObject {
  readonly type: 'BrightPlate';
  readonly queryString: DmParameterizedString;
  readonly brightPlateName: string;
  readonly displayName: string;
}

export type DmHtmlSite = DmLocalHtmlSite | DmRemoteHtmlSite | DmBrightPlateHtmlSite;

// endregion

// region DataFeeds

export interface DmDataFeed extends DmObject {
  readonly type: DataFeedType;
  readonly url: DmParameterizedString | string;
  readonly usage: DataFeedUsageType;
  readonly updateInterval: number;
  readonly useHeadRequest: boolean;
  readonly parserPlugin: BsDmId;
  readonly autoGenerateUserVariables: boolean;
  readonly userVariableAccess: AccessType;
}

export interface DmBsnDataFeed extends DmDataFeed {
  readonly bsnId: number;
  readonly bsnName: string;
  readonly supportsAudio: boolean;
  readonly matchPlayerTags: boolean;
}

// endregion

// region ContentItems

/**
 * DmContentItem serves as the "abstract base interface" for all content item types
 */
export interface DmContentItem {
  readonly name: string;
  readonly type: ContentItemType;
}

/**
 * The actual contentItem of a MediaState will always be of type DmDerivedContentItem
 * In other words - it will be one of the derived content item types.
 */
export type DmDerivedContentItem =
  DmMediaContentItem | DmHtmlContentItem |
  DmDataFeedContentItem | DmMrssDataFeedContentItem |
  DmLiveVideoContentItem;

export type DmDerivedNonMediaContentItem =
  DmHtmlContentItem |
  DmDataFeedContentItem | DmMrssDataFeedContentItem |
  DmLiveVideoContentItem;

export type DmContentItemData =
  DmVideoContentItemData | DmAudioContentItemData | DmImageContentItemData |
  DmHtmlContentItemData | DmMrssDataFeedContentItemData | DmLiveVideoContentItemData |
  DmStreamContentItemData;

export interface DmMediaContentItem extends DmContentItem {
  assetId: string;      // file asset ID - this is resolved externally - typically this is a GUID
}

export interface DmVideoContentItemData {
  volume: number;
  videoDisplayMode: VideoDisplayModeType;
  automaticallyLoop: boolean;
}
export const DmVideoContentItemDataNameArray = ['volume', 'videoDisplayMode', 'automaticallyLoop'];
export interface DmVideoContentItem extends Readonly<DmMediaContentItem>, Readonly<DmVideoContentItemData> {}

export interface DmAudioContentItemData {
  volume: number;
}
export const DmAudioContentItemDataNameArray = ['volume'];
export interface DmAudioContentItem extends Readonly<DmMediaContentItem>, Readonly<DmAudioContentItemData> {}

export interface DmImageContentItemData {
  useImageBuffer: boolean;
  videoPlayerRequired: boolean;
}
export interface DmImageContentItem extends Readonly<DmMediaContentItem>, Readonly<DmImageContentItemData> {}
export interface DmImageContentItemDefaults extends DmImageContentItemData {
  displayDuration: number;
}
export const DmImageContentItemDataNameArray = ['useImageBuffer', 'videoPlayerRequired', 'displayDuration'];

export type DmTextContentItem = Readonly<DmMediaContentItem>;

export interface DmHtmlContentItemData {
  enableExternalData: boolean;
  enableMouseEvents: boolean;
  displayCursor: boolean;
  hwzOn: boolean;
  useUserStylesheet: boolean;
  userStylesheetAssetId?: string;
  customFonts?: DmFont[];
}
export interface DmHtmlContentItem extends DmContentItem, Readonly<DmHtmlContentItemData> {
  readonly siteId: BsDmId;
}
export interface DmHtmlContentItemDefaults extends DmHtmlContentItemData {
  displayDuration: number;
}

export interface DmDataFeedContentItem extends DmContentItem {
  readonly dataFeedId: BsDmId;
}
export const DmHtmlContentItemDataNameArray = [
  'enableExternalData', 'enableMouseEvents', 'displayCursor', 'hwzOn',
  'useUserStylesheet', 'userStylesheetAssetId', 'customFonts', 'displayDuration',
];

export interface DmMrssDataFeedContentItemData {
  videoPlayerRequired: boolean;
}
export const DmMrssDataFeedContentItemDataNameArray = ['videoPlayerRequired'];
export interface DmMrssDataFeedContentItem extends DmDataFeedContentItem, DmMrssDataFeedContentItemData {}

export interface DmLiveVideoContentItemData {
  volume: number;
  overscan: boolean;
}
export interface DmLiveVideoContentItem extends DmContentItem, DmLiveVideoContentItemData {}
export interface DmLiveVideoContentItemDefaults extends DmLiveVideoContentItemData {
  displayDuration: number;
}
export const DmLiveVideoContentItemDataNameArray = ['volume', 'overscan', 'displayDuration'];

export interface DmStreamContentItemData {
  volume: number;
}
export interface DmStreamContentItemDefaults extends DmStreamContentItemData {
  displayDuration: number;
}
export const DmStreamContentItemDataNameArray = ['volume', 'displayDuration'];
export interface DmVideoStreamContentItem extends DmContentItem, DmStreamContentItemData {
  url: DmParameterizedString;
}
export interface DmAudioStreamContentItem extends DmContentItem, DmStreamContentItemData {
  url: DmParameterizedString;
}

// endregion

// region UserVariables

export interface DmUserVariable extends DmObject {
  // Properties
  readonly defaultValue: string;
  readonly isNetworked: boolean;
  readonly access: AccessType;
  readonly dataFeedId: BsDmId;
  readonly systemVariable: string;    // TODO: use enum for SystemVariableType
}

// endregion

// region Conditions

export interface DmCondition extends DmObject {
  // Properties
  readonly userVariable: DmUserVariable;
  readonly compareOperator: CompareOperator;
  readonly compareValue1: string;
  readonly compareValue2: string;
}

// endregion

// region Transitions

export interface DmTransition extends DmObject {
  readonly eventId: BsDmId;
  readonly targetMediaStateId: BsDmId;
  readonly type: TransitionType;
  readonly duration: number;
  readonly condition?: DmCondition;
  // TODO - conditional transition chains must be ordered (linked list?)
}

// endregion

// region Events

export interface DmTimer {
  interval: number;
}
export type DmEventData = DmTimer | undefined;  // To be extended with other event data types

export interface DmEvent extends DmObject {
  readonly type: EventType;
  readonly mediaStateId: BsDmId;
  readonly data: DmEventData;
}

// endregion

// region MediaStates

export enum MediaStateContainerType {
  Zone,
  MediaList,
  SuperState,
}

export interface DmMediaStateContainer {
  id: BsDmId;
  type: MediaStateContainerType;
}

export interface DmMediaState extends DmObject {
  readonly container: DmMediaStateContainer;
  readonly contentItem: DmDerivedContentItem;
}

// endregion

// region Zones

export interface DmZone extends DmObject {
  readonly type: ZoneType;
  readonly tag: string;
  readonly nonInteractive: boolean;
  readonly initialMediaStateId: BsDmId;
  readonly position: BsRect;
  readonly properties: DmZoneSpecificProperties;
}

// Zone IDs mapped by tags
export interface DmZoneTagMap {
  [tag: string]: BsDmId;
}

export type DmZoneSpecificProperties =
  DmAudioZoneProperties | DmVideoZoneProperties | DmImageZoneProperties |
  DmVideoOrImagesZoneProperties | DmEnhancedAudioZoneProperties |
  DmTickerZoneProperties | DmClockZoneProperties | {};

export interface DmAudioZonePropertyData {
  audioOutput: AudioOutputSelectionType;
  audioMode: AudioModeType;
  audioMapping: AudioMappingType;
  audioOutputAssignments: DmAudioOutputAssignmentMap;
  audioMixMode: AudioMixModeType;
  audioVolume: number;
  minimumVolume: number;
  maximumVolume: number;
}
export type DmAudioZoneProperties = Readonly<DmAudioZonePropertyData>;

export interface DmEnhancedAudioZonePropertyData {
  fadeLength: number;
}
export interface DmEnhancedAudioZoneProperties
  extends DmAudioZoneProperties, Readonly<DmEnhancedAudioZonePropertyData> {}

export interface DmImageZonePropertyData {
  imageMode: ImageModeType;
}
export type DmImageZoneProperties = Readonly<DmImageZonePropertyData>;

export interface DmVideoZonePropertyData {
  viewMode: ViewModeType;
  liveVideoInput: LiveVideoInputType;
  liveVideoStandard: LiveVideoStandardType;
  videoVolume: number;
  brightness: number;
  contrast: number;
  saturation: number;
  hue: number;
  zOrderFront: boolean;
  mosaic: boolean;
  maxContentResolution: MosaicMaxContentResolutionType;
  mosaicDecoderName?: string;
}
export interface DmVideoZoneProperties extends DmAudioZoneProperties, Readonly<DmVideoZonePropertyData> {}

export interface DmVideoOrImagesZoneProperties extends DmVideoZoneProperties, DmImageZoneProperties {}

export interface DmWidget {
  foregroundTextColor: BsColor;
  backgroundTextColor: BsColor;
  font: string;
  fontSize: number;
  backgroundBitmapAssetId: string;
  stretchBitmapFile: boolean;
  safeTextRegion: BsRect;
}

export interface DmTextWidget {
  numberOfLines: number;
  delay: number;
  rotation: RotationType;
  alignment: TextHAlignmentType;
  scrollingMethod: TextScrollingMethodType;
}

export interface DmTickerZonePropertyData {
  textWidget: DmTextWidget;
  widget: DmWidget;
  scrollSpeed: number;
}

export interface DmTickerZoneProperties {
  readonly textWidget: Readonly<DmTextWidget>;
  readonly widget: Readonly<DmWidget>;
  readonly scrollSpeed: number;
}

export interface DmClockZonePropertyData {
  displayTime: boolean;
  rotation: RotationType;
  widget: DmWidget;
}
export type DmClockZoneProperties = Readonly<DmClockZonePropertyData>;

// endregion

// region Sign Metadata

export interface DmSignMetadata {
  readonly properties: DmSignProperties;
  readonly serialPortConfigurations: DmSerialPortList;
  readonly gpio: DmGpioList;
  readonly buttonPanels: DmButtonPanelMap;
  readonly audioSignPropertyMap: DmAudioSignPropertyMap;
}

export interface DmSignPropertyData {
  videoMode: VideoMode;
  model: PlayerModel;
  monitorOrientation: MonitorOrientationType;
  monitorOverscan: MonitorOverscanType;
  videoConnector: VideoConnectorType;
  deviceWebPageDisplay: DeviceWebPageDisplay;
  backgroundScreenColor: BsColor;
  forceResolution: boolean;
  tenBitColorEnabled: boolean;
  htmlEnableJavascriptConsole: boolean;
  alphabetizeVariableNames: boolean;
  autoCreateMediaCounterVariables: boolean;
  resetVariablesOnPresentationStart: boolean;
  networkedVariablesUpdateInterval: number;
  delayScheduleChangeUntilMediaEndEvent: boolean;
  language: LanguageType;
  languageKey: LanguageKeyType;
  flipCoordinates: boolean;
  isMosaic: boolean;
  inactivityTimeout: boolean;
  inactivityTime: number;
  touchCursorDisplayMode: TouchCursorDisplayModeType;
  graphicsZOrder: GraphicsZOrderType;
  udpDestinationAddressType: UdpAddressType;
  udpDestinationAddress: string;
  udpDestinationPort: number;
  udpReceiverPort: number;
}

export interface DmSignProperties extends Readonly<DmSignPropertyData> {
  readonly id: BsDmId;
  readonly version: string;
  readonly name: string;
}

// Hardware configuration interfaces for Sign metadata

export interface DmSignHardwareConfiguration {
  readonly serialPortConfigurations: Readonly<DmSerialPortList>;
  readonly gpio: Readonly<DmGpioList>;
  readonly buttonPanels: Readonly<DmButtonPanelMap>;
  readonly audioSignPropertyMap: Readonly<DmAudioSignPropertyMap>;
}

export interface DmSerialPortConfiguration {
  port: number;
  baudRate: number;
  dataBits: number;
  stopBits: number;
  parity: string;
  protocol: string;
  sendEol: string;
  receiveEol: string;
  invertSignals: boolean;
  connectedDevice: string;
}
export type DmSerialPortList = DmSerialPortConfiguration[];

export type DmGpioList = GpioType[];

export interface DmBpConfiguration {
  configureAutomatically: boolean;
  configuration: number;
}

export interface DmButtonPanelMap {
  [bpName: string]: DmBpConfiguration;
}

export interface DmAudioSignProperties {
  min: number;
  max: number;
}

export interface DmAudioSignPropertyMap {
  [audioName: string]: DmAudioSignProperties;
}

export interface DmAudioOutputAssignmentMap {
  [audioName: string]: AudioOutputType;
}

// endregion



