import { createSelector } from 'reselect';

import { ZoneType } from '@brightsign/bscore';
import { DmState } from '../reducers/index';
import { BsDmId, BsDmIdNone, DmIdParam, DmZone } from '../bsDmInterfaces';
import { dmIsValidContentItemForPlaylist } from '../bsDmContentItems';
import { DmMediaStateCollectionState } from '../reducers/reducerMediaState';
import { DmEventCollectionState } from '../reducers/reducerEvent';
import { DmTransitionCollectionState } from '../reducers/reducerTransition';

// If zone media states are a valid simple playlist, return sorted array of mediaStateIds in playlist order.
// If zone media states are not a valid simple playlist, return undefined.
const getOrderedMediaStateIdsForPlaylistInZone = (
  zoneId: BsDmId,
  zoneType: ZoneType,
  initialMediaStateId: BsDmId,
  mediaStates: DmMediaStateCollectionState,
  events: DmEventCollectionState,
  transitions: DmTransitionCollectionState,
): BsDmId[] | null => {
  const mediaStateIds: BsDmId[] =
    Object.keys(mediaStates).filter((id, index, idArray): boolean => mediaStates[id].container.id === zoneId);
  const sortedMediaStateIds: BsDmId[] = [];
  let nextMediaStateId = initialMediaStateId;
  if (nextMediaStateId !== BsDmIdNone && mediaStateIds.length > 0) {
    while (true) {
      const index = mediaStateIds.indexOf(nextMediaStateId);
      if (index > -1) {
        mediaStateIds.splice(index, 1);
      }
      const mediaStateState = mediaStates[nextMediaStateId];
      if (!mediaStateState || !dmIsValidContentItemForPlaylist(mediaStateState.contentItem, zoneType)) {
        // In a valid playlist, the mediaState must hold only certain content items (e.g., media, HTML)
        return null;
      }
      const eventListIds =
        Object.keys(events).filter((id): boolean => events[id].mediaStateId === nextMediaStateId);
      if (!eventListIds || eventListIds.length !== 1) {
        // In a valid playlist, each mediaState will have only one event
        return null;
      }
      const transitionIds =
        Object.keys(transitions).filter((id): boolean => transitions[id].eventId === eventListIds[0]);
      if (!transitionIds || transitionIds.length !== 1) {
        // In a valid playlist, the event will have only one transition
        return null;
      }
      const transitionState = transitions[transitionIds[0]];
      if (nextMediaStateId === transitionState.targetMediaStateId
        && (nextMediaStateId !== initialMediaStateId || mediaStateIds.length > 0)) {
        // If transition target is same state, and it is NOT the initialMediaState or there are
        //  more mediaStates in the zone, this is not a valid playlist
        return null;
      }
      sortedMediaStateIds.push(nextMediaStateId);
      nextMediaStateId = transitionState.targetMediaStateId;
      if (nextMediaStateId === initialMediaStateId) {
        // If the transition target is the initial state, we are done
        if (mediaStateIds.length > 0) {
          // There should be no remaining states, otherwise it is not a valid playlist
          return null;
        }
        break;
      }
    }
  }
  return sortedMediaStateIds;
};

/** @internal */
/** @private */
export const getZoneSimplePlaylist = createSelector<
  DmState, BsDmId[] | null, DmZone, DmMediaStateCollectionState,
  DmEventCollectionState, DmTransitionCollectionState>
(
  (state: DmState, props: DmIdParam) => state.zones.zonesById[props.id],
  (state: DmState) => state.mediaStates,
  (state: DmState) => state.events,
  (state: DmState) => state.transitions,
  (zone, mediaStates, events, transitions): BsDmId[] => {
    return zone ?
      getOrderedMediaStateIdsForPlaylistInZone(zone.id, zone.type,
        zone.initialMediaStateId, mediaStates, events, transitions) :
      null;
  },
);



