import { createSelector } from 'reselect';
import { omit } from 'lodash';

import { EventType } from '@brightsign/bscore';
import { BsDmId, DmEventData, DmEvent, DmIdParam } from '../bsDmInterfaces';
import { EventParams, EventAction, ADD_EVENT, UPDATE_EVENT, DELETE_EVENT } from '../actions/actionEvent';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { DELETE_ZONE } from '../actions/actionZone';
import { MediaStateDeleteParams, MediaStateDeleteAction, DELETE_MEDIA_STATE } from '../actions/actionMediaState';
import { BsDmAction } from '../actions/actionBase';
import { DmMap } from './index';

export type DmEventMap = DmMap<DmEvent> | {};

const createEventState = (
  id: BsDmId,
  name: string,
  type: EventType,
  mediaStateId: BsDmId,
  data: DmEventData,
): DmEvent => ({
  id, name, type, mediaStateId, data,
});

const eventsById = (
  state: DmEventMap = {},
  action: (EventAction | MediaStateDeleteAction | OpenSignAction),
): DmEventMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN: {
      const {newState} = payload as OpenSignParams;
      return newState.events;
    }
    case ADD_EVENT: {
      const {id, name, type: eventType, mediaStateId, data} = payload as EventParams;
      return Object.assign({}, state, {[id]: createEventState(id, name, eventType, mediaStateId, data)});
    }
    case UPDATE_EVENT: {
      const {id} = payload as EventParams;
      const updatedEvent = Object.assign({}, state[id], payload as EventParams);
      return Object.assign({}, state, {[id]: updatedEvent});
    }
    case DELETE_EVENT:
      return omit(state, (payload as EventParams).id);
    case DELETE_MEDIA_STATE:
    case DELETE_ZONE:
      // Array of Event IDs to delete are added to the payload by the action thunk function
      const {eventIds} = payload as MediaStateDeleteParams;
      if (eventIds && eventIds.length) {
        return omit(state, eventIds);
      }
  }
  return state;
};

const eventReducer = eventsById;

/** @internal */
/** @private */
export default eventReducer;

export type DmEventCollectionState = DmEventMap;

/** @internal */
/** @private */
export const isValidEventState = (state: DmEventCollectionState): boolean => {
  // TODO: check contained events for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getEventStateById = (state: DmEventCollectionState, props: DmIdParam): DmEvent | null => {
  const eventState = state[props.id];
  return eventState ? eventState : null;
};

const getEventsById = (state: DmEventCollectionState) => state;

/** @internal */
/** @private */
export const getEventIdsForMediaState = createSelector<DmEventCollectionState, BsDmId[], DmEventMap, BsDmId> (
  getEventsById,
  (_, props: DmIdParam) => props.id as BsDmId,
  (events, mediaStateId) => {
    return Object.keys(events).filter((id, index, idArray): boolean => events[id].mediaStateId === mediaStateId);
  },
);



