import { createSelector } from 'reselect';
import { omit } from 'lodash';

import { TransitionType } from '@brightsign/bscore';
import { BsDmId, DmTransition, DmCondition, DmIdParam } from '../bsDmInterfaces';
import { DmcTransition } from '../bsDmClasses';
import {
  TransitionParams, TransitionAction, ADD_TRANSITION, UPDATE_TRANSITION, DELETE_TRANSITION,
} from '../actions/actionTransition';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { DELETE_ZONE } from '../actions/actionZone';
import { MediaStateDeleteParams, MediaStateDeleteAction, DELETE_MEDIA_STATE } from '../actions/actionMediaState';
import { EventParams, EventAction, DELETE_EVENT } from '../actions/actionEvent';
import { DmMap } from './index';

export type DmTransitionMap = DmMap<DmTransition> | {};

// Return a simple object conforming to DmTransition
const createTransitionState = (
  id: BsDmId,
  name: string,
  eventId: BsDmId,
  targetMediaStateId: BsDmId,
  transitionType: TransitionType,
  duration: number,
  condition?: DmCondition,
): DmTransition =>
  ({
    id,
    name,
    eventId,
    targetMediaStateId,
    type: transitionType,
    duration,
    condition,
  });

const transitionsById = (
  state: DmTransitionMap = {},
  action: (TransitionAction | EventAction | MediaStateDeleteAction | OpenSignAction),
): DmTransitionMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.transitions;
    case ADD_TRANSITION: {
      const { id, name, eventId, targetMediaStateId,
            type: transitionType, duration, condition} = payload as TransitionParams;
      return Object.assign({}, state,
        {[id]: createTransitionState(id, name, eventId, targetMediaStateId, transitionType, duration, condition)});
    }
    case UPDATE_TRANSITION: {
      const {id} = payload as TransitionParams;
      const updatedTransition = Object.assign({}, state[id], payload as TransitionParams);
      return Object.assign({}, state, {[id]: updatedTransition});
    }
    case DELETE_TRANSITION:
      return omit(state, (payload as TransitionParams).id);
    case DELETE_EVENT:
      const eventId = (payload as EventParams).id;
      const newTransitionState: DmTransitionMap = {};
      let transitionsDeleted = false;
      // Filter out all transitions associated with deleted media state
      Object.keys(state).forEach((transitionId) => {
        if (state[transitionId].eventId !== eventId) {
          newTransitionState[transitionId] = state[transitionId];
        } else {
          transitionsDeleted = true;
        }
      });
      if (transitionsDeleted) {
        return newTransitionState;
      }
    case DELETE_MEDIA_STATE:
    case DELETE_ZONE:
      // Array of Transition IDs to delete are added to the payload by the action thunk function
      const {transitionIds} = payload as MediaStateDeleteParams;
      if (transitionIds && transitionIds.length) {
        return omit(state, transitionIds);
      }
  }
  return state;
};

const transitionReducer = transitionsById;

/** @internal */
/** @private */
export default transitionReducer;

export type DmTransitionCollectionState = DmTransitionMap;

/** @internal */
/** @private */
export const isValidTransitionState = (state: DmTransitionCollectionState): boolean => {
  // TODO: check contained transitions for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getTransitionStateById = (
  state: DmTransitionCollectionState,
  props: DmIdParam,
): DmTransition | null => {
  const transitionState = state[props.id];
  return transitionState ? transitionState : null;
};

/** @internal */
/** @private */
export const getTransitionById = (state: DmTransitionCollectionState, props: DmIdParam): DmcTransition | null => {
  const transitionState = state[props.id];
  return transitionState ? new DmcTransition(transitionState) : null;
};

const getTransitionsById = (state: DmTransitionCollectionState) => state;

/** @internal */
/** @private */
export const getTransitionIdsForEvent = createSelector<
  DmTransitionCollectionState, BsDmId[], DmTransitionMap, BsDmId>
(
  getTransitionsById,
  (_, props: DmIdParam) => props.id as BsDmId,
  (transitions, eventId) => {
    return Object.keys(transitions).filter((id, index, idArray): boolean => transitions[id].eventId === eventId);
  },
);



