import { combineReducers, Action } from 'redux';

import { VideoMode, PlayerModel } from '@brightsign/bscore';
import {
  BsDmId, BsDmIdNone, BsDmStateVersion,
  DmSignMetadata, DmSignProperties, DmSerialPortConfiguration, DmSerialPortList, DmGpioList,
  DmButtonPanelMap, DmAudioSignPropertyMap,
} from '../bsDmInterfaces';
import { DmcSignMetadata } from '../bsDmClasses';
import {
  dmGetDefaultSignPropertyData, dmGetDefaultSerialPortList, dmGetDefaultGpioList,
  dmGetDefaultButtonPanelMap, dmGetDefaultAudioSignPropertyMap,
} from '../bsDmDefaults';
import {
  SignAction, SignParams, OpenSignAction, OpenSignParams,
  SerialPortListAction, SerialPortListParams, GpioListAction, GpioListParams,
  ButtonPanelMapAction, ButtonPanelMapParams, AudioSignPropertyMapAction, AudioSignPropertyMapParams,
  NEW_SIGN, OPEN_SIGN, UPDATE_SIGN_PROPS, UPDATE_SIGN_SERIAL_PORTS,
  UPDATE_SIGN_GPIO, UPDATE_SIGN_BPMAP, UPDATE_SIGN_AUDIO_CONFIG,
} from '../actions/actionSign';

// Return an object conforming to default DmSignMetadata
// This is for testing only, as reducers generate each component
/** @internal */
/** @private */
export const createDefaultSignState = (
  id: BsDmId,
  name: string,
  videoMode?: VideoMode,
  model?: PlayerModel,
): DmSignMetadata =>
  ({
    properties: createDefaultSignProperties(id, name, videoMode, model),
    serialPortConfigurations: dmGetDefaultSerialPortList(),
    gpio: dmGetDefaultGpioList(),
    buttonPanels: dmGetDefaultButtonPanelMap(),
    audioSignPropertyMap: dmGetDefaultAudioSignPropertyMap(),
  });

/** @internal */
/** @private */
export const createDefaultSignProperties = (
  id: BsDmId,
  name: string,
  videoMode?: VideoMode,
  model?: PlayerModel,
): DmSignProperties => {
  const optionals = videoMode !== undefined ? {videoMode} : {};
  if (model !== undefined) {
    Object.assign(optionals, {model});
  }
  return Object.assign(
    {id, version: BsDmStateVersion, name},
    dmGetDefaultSignPropertyData(),
    optionals,
  );
};

const newSignProperties = createDefaultSignProperties(BsDmIdNone, 'Untitled');

const properties = (
  state: DmSignProperties = newSignProperties,
  {type, payload}: (SignAction | OpenSignAction),
): DmSignProperties => {
  switch (type) {
    case NEW_SIGN:
    case UPDATE_SIGN_PROPS:
      return Object.assign({}, state, payload as SignParams);
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.sign.properties;
  }
  return state;
};

const serialPortConfigurations = (
  state: DmSerialPortList = dmGetDefaultSerialPortList(),
  {type, payload}: (OpenSignAction | SerialPortListAction),
): DmSerialPortList => {
  switch (type) {
    case NEW_SIGN:
      return dmGetDefaultSerialPortList();
    case UPDATE_SIGN_SERIAL_PORTS: {
      const updateList = (payload as SerialPortListParams).params;
      updateList.forEach((portConfig: DmSerialPortConfiguration) => {
        const portNum = portConfig.port;
        state[portNum] = Object.assign({}, state[portNum], portConfig);
      });
      return state;
    }
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.sign.serialPortConfigurations;
  }
  return state;
};

const gpio = (
  state: DmGpioList = dmGetDefaultGpioList(),
  {type, payload}: (OpenSignAction | GpioListAction),
): DmGpioList => {
  switch (type) {
    case NEW_SIGN:
      return dmGetDefaultGpioList();
    case UPDATE_SIGN_GPIO: {
      return (payload as GpioListParams).params;
    }
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.sign.gpio;
  }
  return state;
};

const buttonPanels = (
  state: DmButtonPanelMap = dmGetDefaultButtonPanelMap(),
  {type, payload}: (OpenSignAction | ButtonPanelMapAction),
): DmButtonPanelMap => {
  switch (type) {
    case NEW_SIGN:
      return dmGetDefaultButtonPanelMap();
    case UPDATE_SIGN_BPMAP: {
      const updateList = (payload as ButtonPanelMapParams).params;
      Object.keys(updateList).forEach((bpName: string) => {
        state[bpName] = Object.assign({}, state[bpName], updateList[bpName]);
      });
      return state;
    }
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.sign.buttonPanels;
  }
  return state;
};

const audioSignPropertyMap = (
  state: DmAudioSignPropertyMap = dmGetDefaultAudioSignPropertyMap(),
  {type, payload}: (OpenSignAction | AudioSignPropertyMapAction),
): DmAudioSignPropertyMap => {
  switch (type) {
    case NEW_SIGN:
      return dmGetDefaultAudioSignPropertyMap();
    case UPDATE_SIGN_AUDIO_CONFIG: {
      const updateList = (payload as AudioSignPropertyMapParams).params;
      Object.keys(updateList).forEach((audioName: string) => {
        state[audioName] = Object.assign({}, state[audioName], updateList[audioName]);
      });
      return state;
    }
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.sign.audioSignPropertyMap;
  }
  return state;
};

const signReducer = combineReducers<DmSignMetadata>(
  {properties, serialPortConfigurations, gpio, buttonPanels, audioSignPropertyMap});

/** @internal */
/** @private */
export default signReducer;

/** @internal */
/** @private */
export const isValidSignState = (state: DmSignMetadata): boolean => {
  return typeof state === 'object'
    && state.hasOwnProperty('properties')
    && state.properties.hasOwnProperty('version')
    && state.properties.hasOwnProperty('name')
    && state.properties.hasOwnProperty('videoMode')
    && state.properties.hasOwnProperty('model')
    && state.properties.hasOwnProperty('monitorOrientation')
    && state.properties.hasOwnProperty('videoConnector')
    && state.properties.hasOwnProperty('deviceWebPageDisplay')
    && state.properties.hasOwnProperty('backgroundScreenColor')
    && state.properties.hasOwnProperty('forceResolution')
    && state.properties.hasOwnProperty('tenBitColorEnabled')
    && state.properties.hasOwnProperty('htmlEnableJavascriptConsole')
    && state.properties.hasOwnProperty('alphabetizeVariableNames')
    && state.properties.hasOwnProperty('autoCreateMediaCounterVariables')
    && state.properties.hasOwnProperty('resetVariablesOnPresentationStart')
    && state.properties.hasOwnProperty('networkedVariablesUpdateInterval')
    && state.properties.hasOwnProperty('delayScheduleChangeUntilMediaEndEvent')
    && state.properties.hasOwnProperty('language')
    && state.properties.hasOwnProperty('languageKey')
    && state.properties.hasOwnProperty('flipCoordinates')
    && state.properties.hasOwnProperty('isMosaic')
    && state.properties.hasOwnProperty('inactivityTimeout')
    && state.properties.hasOwnProperty('inactivityTime')
    && state.properties.hasOwnProperty('touchCursorDisplayMode')
    && state.properties.hasOwnProperty('graphicsZOrder')
    && state.properties.hasOwnProperty('udpDestinationAddressType')
    && state.properties.hasOwnProperty('udpDestinationAddress')
    && state.properties.hasOwnProperty('udpDestinationPort')
    && state.properties.hasOwnProperty('udpReceiverPort')
    && state.hasOwnProperty('serialPortConfigurations')
    && state.hasOwnProperty('gpio')
    && state.hasOwnProperty('buttonPanels')
    && state.hasOwnProperty('audioSignPropertyMap');
};

// Selectors

/** @internal */
/** @private */
export const getSignMetadata = (state: DmSignMetadata) => new DmcSignMetadata(state);
/** @internal */
/** @private */
export const getSignId = (state: DmSignMetadata) => state.properties.id;
/** @internal */
/** @private */
export const getSignName = (state: DmSignMetadata) => state.properties.name;
/** @internal */
/** @private */
export const getSignVideoMode = (state: DmSignMetadata) => state.properties.videoMode;
/** @internal */
/** @private */
export const getSignPlayerModel = (state: DmSignMetadata) => state.properties.model;
/** @internal */
/** @private */
export const getSignSerialPortConfigurations = (state: DmSignMetadata) => state.serialPortConfigurations;
/** @internal */
/** @private */
export const getSignGpioList = (state: DmSignMetadata) => state.gpio;
/** @internal */
/** @private */
export const getSignButtonPanelMap = (state: DmSignMetadata) => state.buttonPanels;
/** @internal */
/** @private */
export const getSignAudioPropertyMap = (state: DmSignMetadata) => state.audioSignPropertyMap;



