import { createSelector } from 'reselect';
import { omit } from 'lodash';

import { AccessType, DataFeedType, DataFeedUsageType, BsnFeedProperties } from '@brightsign/bscore';
import { BsDmId, DmDataFeed, DmBsnDataFeed, DmParameterizedString, DmIdParam, DmNameParam } from '../bsDmInterfaces';
import { DmcDataFeed } from '../bsDmClasses';
import {
  DataFeedAction, DataFeedParams, ADD_DATA_FEED, UPDATE_DATA_FEED,
  BsnDataFeedAction, BsnDataFeedParams, ADD_BSN_DATA_FEED, UPDATE_BSN_DATA_FEED,
  DELETE_DATA_FEED,
} from '../actions/actionDataFeed';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { DmMap } from './index';

export type DmDataFeedMap = DmMap<DmDataFeed> | {};

const createDataFeed = (
  id: BsDmId,
  name: string,
  url: DmParameterizedString,
  usage: DataFeedUsageType,
  updateInterval: number,
  useHeadRequest: boolean,
  parserPlugin: BsDmId,
  autoGenerateUserVariables: boolean,
  userVariableAccess: AccessType,
): DmDataFeed => ({
  id, name, type: DataFeedType.URLDataFeed, url, usage, updateInterval,
  useHeadRequest, parserPlugin, autoGenerateUserVariables, userVariableAccess,
});

const createBsnDataFeed = (
  id: BsDmId,
  name: string,
  bsnDataFeed: BsnFeedProperties,
  usage: DataFeedUsageType,
  updateInterval: number,
  useHeadRequest: boolean,
  parserPlugin: BsDmId,
  autoGenerateUserVariables: boolean,
  userVariableAccess: AccessType,
  supportsAudio: boolean,
  matchPlayerTags: boolean,
): DmBsnDataFeed => ({
  id,
  name,
  type: bsnDataFeed.type,
  url: bsnDataFeed.physicalPath,
  bsnId: bsnDataFeed.id,
  bsnName: bsnDataFeed.name,
  usage, updateInterval, useHeadRequest, parserPlugin,
  autoGenerateUserVariables, userVariableAccess,
  supportsAudio, matchPlayerTags,
});

const dataFeedsById = (
  state: DmDataFeedMap = {},
  action: (DataFeedAction | BsnDataFeedAction | OpenSignAction),
): DmDataFeedMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      // if undefined in given sign, create empty object
      return newState.dataFeeds ? newState.dataFeeds : {};
    case ADD_DATA_FEED: {
      const { id, name, url, usage,
        updateInterval, useHeadRequest, parserPlugin,
        autoGenerateUserVariables, userVariableAccess } = payload as DataFeedParams;
      return Object.assign({}, state,
        {[id]: createDataFeed(id, name, url as DmParameterizedString, usage,
        updateInterval, useHeadRequest, parserPlugin, autoGenerateUserVariables, userVariableAccess)});
    }
    case ADD_BSN_DATA_FEED: {
      const { id, name, bsnDataFeed, usage,
        updateInterval, useHeadRequest, parserPlugin,
        autoGenerateUserVariables, userVariableAccess, supportsAudio, matchPlayerTags } = payload as BsnDataFeedParams;
      return Object.assign({}, state,
        {[id]: createBsnDataFeed(id, name, bsnDataFeed, usage,
          updateInterval, useHeadRequest, parserPlugin, autoGenerateUserVariables, userVariableAccess,
          supportsAudio, matchPlayerTags)});
    }
    case UPDATE_DATA_FEED: {
      const {id} = payload as DataFeedParams;
      const updatedDataFeed = Object.assign({}, state[id], payload as DataFeedParams);
      return Object.assign({}, state, {[id]: updatedDataFeed});
    }
    case UPDATE_BSN_DATA_FEED: {
      const {id} = payload as BsnDataFeedParams;
      const updatedDataFeed = Object.assign({}, state[id], payload as BsnDataFeedParams);
      return Object.assign({}, state, {[id]: updatedDataFeed});
    }
    case DELETE_DATA_FEED:
      return omit(state, (payload as DataFeedParams).id);
  }
  return state;
};

const dataFeedReducer = dataFeedsById;

/** @internal */
/** @private */
export default dataFeedReducer;

export type DmDataFeedCollectionState = DmDataFeedMap;

/** @internal */
/** @private */
export const isValidDataFeedState = (state: DmDataFeedCollectionState): boolean => {
  // TODO: check contained Data Feeds for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getDataFeedById = (state: DmDataFeedCollectionState, props: DmIdParam): DmcDataFeed | null => {
  return state[props.id] ? new DmcDataFeed(state[props.id]) : null;
};

/** @internal */
/** @private */
export const getDataFeedIdsForSign = (state: DmDataFeedCollectionState) => Object.keys(state);

/** @internal */
/** @private */
export const getDataFeedNamesForSign = createSelector<DmDataFeedCollectionState, string[], DmDataFeedMap> (
  (state: DmDataFeedCollectionState) => state,
  (dataFeeds) => {
    const dataFeedNames: string[] = [];
    Object.keys(dataFeeds).forEach((id) => {
      dataFeedNames.push(dataFeeds[id].name);
    });
    return dataFeedNames;
  },
);

/** @internal */
/** @private */
export const getDataFeedByName = createSelector<DmDataFeedCollectionState, DmcDataFeed | null, DmDataFeedMap, string> (
  (state: DmDataFeedCollectionState) => state,
  (_, props: DmNameParam) => props.name,
  (dataFeeds, name) => {
    let dataFeed: DmDataFeed;
    Object.keys(dataFeeds).some((id): boolean => {
      if (dataFeeds[id].name === name) {
        dataFeed = dataFeeds[id];
        return true;
      }
    });
    return dataFeed ? new DmcDataFeed(dataFeed) : null;
  },
);



