import {BsDmBaseAction} from './actions/actionBase';

export enum DmErrorType {
  unknownError,
  unexpectedError,
  invalidParameters,
  invalidOperation,
  apiError,
  invalidSign,
}

const bsnErrorMessage: {[type: number]: string} = {
  [DmErrorType.unknownError] : 'Unknown error',
  [DmErrorType.unexpectedError] : 'Unexpected error',
  [DmErrorType.invalidParameters] : 'Invalid parameters',
  [DmErrorType.invalidOperation] : 'Invalid operation attempt',
  [DmErrorType.apiError] : 'API error',
  [DmErrorType.invalidSign] : 'Invalid presentation',
};

export class BsDmError extends Error {
  name = 'BsDmError';
  type: DmErrorType;
  action?: BsDmBaseAction;

  constructor(type: DmErrorType, reason?: string, action?: BsDmBaseAction) {
    super();
    this.type = type;
    if (reason) {
      this.message = bsnErrorMessage[type] + ': ' + reason;
    } else {
      this.message = bsnErrorMessage[type];
    }
    if (action) {
      this.action = Object.assign({}, action, {error: true, meta: {reason: this.message}});
    }
    Object.setPrototypeOf(this, BsDmError.prototype);
  }

  attachAction(action: BsDmBaseAction) {
    this.action = Object.assign({}, action, {error: true, meta: {reason: this.message}});
  }
}

export function dmIsBsDmError(error: Error): error is BsDmError {
  return error instanceof BsDmError;
}



