import { createSelector } from 'reselect';
import { omit, cloneDeep } from 'lodash';

import { ContentItemType } from '@brightsign/bscore';
import { BsDmId, DmMediaState, DmIdParam, DmNameParam } from '../bsDmInterfaces';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { DELETE_ZONE } from '../actions/actionZone';
import {
  MediaStateParams, MediaStateAction, MediaStateDeleteParams, MediaStateDeleteAction,
  ADD_MEDIA_STATE, UPDATE_MEDIA_STATE, DELETE_MEDIA_STATE,
} from '../actions/actionMediaState';
import { BsDmAction } from '../actions/actionBase';
import { DmMap } from './index';

// TODO: for super states, add reducer for SET_INITIAL_MEDIA_STATE

export type DmMediaStateMap = DmMap<DmMediaState> | {};

const mediaStatesById = (
  state: DmMediaStateMap = {},
  action: (MediaStateAction | MediaStateDeleteAction | OpenSignAction),
): DmMediaStateMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      return newState.mediaStates;
    case ADD_MEDIA_STATE: {
      const {id, name, container, contentItem} = payload as MediaStateParams;
      const containerCopy = cloneDeep(container);
      const contentItemCopy = cloneDeep(contentItem);
      return Object.assign({}, state, {[id]: { id, name, container: containerCopy, contentItem: contentItemCopy }});
    }
    case UPDATE_MEDIA_STATE: {
      const {id} = payload as MediaStateParams;
      if (state[id] !== undefined) {
        const payloadCopy = payload as MediaStateParams;
        if (payload.hasOwnProperty('contentItem') || payload.hasOwnProperty('container')) {
          if (payload.hasOwnProperty('contentItem')) {
            payloadCopy.contentItem = cloneDeep(payloadCopy.contentItem);
          }
          if (payload.hasOwnProperty('container')) {
            payloadCopy.container = cloneDeep(payloadCopy.container);
          }
        }
        const updatedMediaState = Object.assign({}, state[id], payloadCopy);
        return Object.assign({}, state, {[id]: updatedMediaState});
      }
    }
    case DELETE_MEDIA_STATE:
    case DELETE_ZONE:
      // Array of MediaState IDs to delete are added to the payload by the action thunk function
      const {mediaStateIds} = payload as MediaStateDeleteParams;
      if (mediaStateIds && mediaStateIds.length) {
        return omit(state, mediaStateIds);
      }
  }
  return state;
};

const mediaStateReducer = mediaStatesById;

/** @internal */
/** @private */
export default mediaStateReducer;

export type DmMediaStateCollectionState = DmMediaStateMap;

/** @internal */
/** @private */
export const isValidMediaStateState = (state: DmMediaStateCollectionState): boolean => {
  // TODO: check contained media states for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getMediaStateStateById =
  (state: DmMediaStateCollectionState, props: DmIdParam): DmMediaState | null => {
  const mediaState = state[props.id];
  return mediaState ? mediaState : null;
};

/** @internal */
/** @private */
export const getMediaStateStateByName = createSelector<
  DmMediaStateCollectionState, DmMediaState | null, DmMediaStateMap, string>
(
  (state: DmMediaStateCollectionState) => state,
  (_, props: DmNameParam) => props.name as BsDmId,
  (states, name) => {
    let mediaStateState: DmMediaState;
    Object.keys(states).some((id): boolean => {
      if (states[id].name === name) {
        mediaStateState = states[id];
        return true;
      }
    });
    return mediaStateState ? mediaStateState : null;
  },
);

/** @internal */
/** @private */
export const getMediaStateIdsForZone = createSelector<
  DmMediaStateCollectionState, BsDmId[], DmMediaStateMap, BsDmId>
(
  (state: DmMediaStateCollectionState) => state,
  (_, props: DmIdParam) => props.id as BsDmId,
  (states, zoneId) => {
    return Object.keys(states).filter((id, index, idArray): boolean => states[id].container.id === zoneId);
  },
);

/** @internal */
/** @private */
export const getContentItemTypeForMediaStateId = (
  state: DmMediaStateCollectionState,
  props: DmIdParam,
): ContentItemType | null => {
  const mediaState = state[props.id];
  return mediaState ? mediaState.contentItem.type : null;
};

// To be used in React Redux mapStateToProps, so that zone list memoization will be efficient for multiple zones
/** @internal */
/** @private */
export const makeGetMediaStatesForZone = () => {
  return getMediaStateIdsForZone;
};



