import { createSelector } from 'reselect';
import { omit } from 'lodash';

import { AccessType } from '@brightsign/bscore';
import { BsDmId, DmUserVariable, DmParameterizedString, DmIdParam, DmNameParam } from '../bsDmInterfaces';
import {
  UserVariableAction, UserVariableParams,
  UserVariablesDeleteAction, UserVariablesDeleteParams,
  ADD_USER_VARIABLE, UPDATE_USER_VARIABLE, DELETE_USER_VARIABLE, DELETE_USER_VARIABLES,
} from '../actions/actionUserVariable';
import { OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';
import { BsDmAction } from '../actions/actionBase';
import { DmMap } from './index';

export type DmUserVariableMap = DmMap<DmUserVariable> | {};

const createUserVariable = (
  id: BsDmId,
  name: string,
  defaultValue: string,
  access: AccessType,
  isNetworked: boolean,
  dataFeedId: BsDmId,
  systemVariable: string,
): DmUserVariable => ({
  id, name, defaultValue, access, isNetworked, dataFeedId, systemVariable,
});

const userVariablesById = (
  state: DmUserVariableMap = {},
  action: (UserVariableAction | UserVariablesDeleteAction | OpenSignAction),
): DmUserVariableMap => {
  const {type, payload} = action;
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN:
      const {newState} = payload as OpenSignParams;
      // if undefined in given sign, create empty object
      return newState.userVariables ? newState.userVariables : {};
    case ADD_USER_VARIABLE: {
      const { id, name, defaultValue, access, isNetworked, dataFeedId, systemVariable } = payload as UserVariableParams;
      return Object.assign({}, state,
        {[id]: createUserVariable(id, name, defaultValue, access, isNetworked, dataFeedId, systemVariable)});
    }
    case UPDATE_USER_VARIABLE: {
      const {id} = payload as UserVariableParams;
      const updatedUserVariable = Object.assign({}, state[id], payload as UserVariableParams);
      return Object.assign({}, state, {[id]: updatedUserVariable});
    }
    case DELETE_USER_VARIABLE:
      return omit(state, (payload as UserVariableParams).id);
    case DELETE_USER_VARIABLES: {
      const {ids} = payload as UserVariablesDeleteParams;
      return omit(state, ids);
    }
  }
  return state;
};

/** @internal */
/** @private */
const userVariableReducer = userVariablesById;

/** @internal */
/** @private */
export default userVariableReducer;

export type DmUserVariableCollectionState = DmUserVariableMap;

/** @internal */
/** @private */
export const isValidUserVariableState = (state: DmUserVariableCollectionState): boolean => {
  // TODO: check contained Data Feeds for validity
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getUserVariableById = (state: DmUserVariableCollectionState, props: DmIdParam): DmUserVariable | null => {
  return state[props.id] ? Object.assign({}, state[props.id]) : null;
};

/** @internal */
/** @private */
export const getUserVariableIdsForSign = (state: DmUserVariableCollectionState) => Object.keys(state);

const getUserVariablesById = (state: DmUserVariableCollectionState) => state;

/** @internal */
/** @private */
export const getUserVariableByName = createSelector<
  DmUserVariableCollectionState, DmUserVariable | null, DmUserVariableMap, string>
(
  getUserVariablesById,
  (_, props: DmNameParam) => props.name as BsDmId,
  (userVariables, name) => {
    let userVariable: DmUserVariable;
    Object.keys(userVariables).some((id): boolean => {
      if (userVariables[id].name === name) {
        userVariable = userVariables[id];
        return true;
      }
    });
    return userVariable ? Object.assign({}, userVariable) : null;
  },
);



