import {MediaType, BsAssetItem, BsLocalAssetThumbnail} from '@brightsign/bscore';
import * as path from 'path';
import * as fse from 'fs-extra';
import * as tmp from 'tmp';
import * as ffmpeg from 'fluent-ffmpeg';
import * as resizeImg from 'resize-img';
import * as parsePng from 'parse-png';
import * as jpeg from 'jpeg-js';
import * as crypto from 'crypto';
import * as sizeOf from 'image-size';

import {FsErrorType, FsError} from './error';

ffmpeg().setFfmpegPath('//usr//local//bin//ffmpeg');
// ffmpeg().setFfmpegPath(remote.app.getAppPath() + '/ffmpeg'); TODO save for app packaging

/**
 * Generate a FileThumbnail for the given asset file. Asset file must be a video or an image file
 * @param assetItem {BsAssetItem} - asset item for which to generate thumbnail
 * @returns {Promise<BsAssetThumbnail | null>} - returns a Promise. For video and supported image files,
 *  promise resolves with thumbnail object. For unsupported asset files, promise resolves with null.
 */
export function getThumbnail(assetItem: BsAssetItem): Promise<BsLocalAssetThumbnail | null> {

  if (assetItem.mediaType === MediaType.Image || assetItem.mediaType === MediaType.Video) {
    const fullPath = path.join(assetItem.path, assetItem.name);
    if (assetItem.mediaType === MediaType.Image) {
      return buildImageThumb(fullPath);
    } else if (assetItem.mediaType === MediaType.Video) {
      return buildVideoThumb(fullPath);
    }
  }
  // Resolve with null for all other files
  return Promise.resolve(null);
}

/** @internal */
/** @private */
export function buildImageThumb(fullPath: string): Promise<BsLocalAssetThumbnail> {
  if (!fse.existsSync(fullPath)) {
    return Promise.resolve(null);
  }
  return new Promise<BsLocalAssetThumbnail>((resolve, reject) => {

    fse.readFile(fullPath)
      .then((buf) => resizeImg(buf, {height: 120, out: 'jpg'}))
      .then((buf) => {
        const thumbSize = sizeOf(buf);
        resolve({
          kind: 'local',
          data: buf as Uint8Array,
          type: 'image/jpeg',
          size: {width: thumbSize.width, height: thumbSize.height},
          hash: getBufferSha1(buf),
        });
      })
      .catch((error) => reject(error));
  });
}

/** @internal */
/** @private */
export function buildVideoThumb(fullPath: string): Promise<BsLocalAssetThumbnail> {
  if (!fse.existsSync(fullPath)) {
    return Promise.resolve(null);
  }
  return new Promise<BsLocalAssetThumbnail>((resolve, reject) => {
    tmp.tmpName({prefix: 'bsthmb-', postfix: '.png'}, (error, thumbPath) => {
      if (error) {
        reject(error);
      }

      try {
        ffmpeg(fullPath)
          .on('end', () => {
            fse.readFile(thumbPath)
              .then((buf) => { fse.unlink(thumbPath); return parsePng(buf); })
              .then((img) => {
                const buf = jpeg.encode({width: img.width, height: img.height, data: img.data}).data;
                resolve({
                    kind: 'local',
                    data: buf as Uint8Array,
                    type: 'image/jpeg',
                    size: {width: img.width, height: img.height},
                    hash: getBufferSha1(buf),
                });
              })
              .catch((err) => reject(err));
          })
          .on('error', (error2) => {
              reject(error2);
          })
          .screenshots({
            filename: path.basename(thumbPath),
            timestamps: [2],
            folder: path.dirname(thumbPath),
            size: '?x120',
          });
      } catch (error3) {
        reject(error3);
      }

    });
  });
}

/**
 * Return SHA1 hash for given file
 * @param fullPath {string} - path to file
 * @returns {Promise<string>} returns a Promise that resolves to the hsh value
 */
export function getFileSha1(fullPath: string): Promise<string> {
  if (!fse.existsSync(fullPath)) {
    return Promise.reject(new FsError(FsErrorType.fileNotFoundError, fullPath));
  }

  return new Promise<string> ((resolve) => {
    const rs = fse.createReadStream(fullPath);
    const hash = crypto.createHash('sha1');
    hash.setEncoding('hex');

    rs.on('end', () => {
      hash.end();
      resolve(hash.read() as string);
    });
    // read all file and pipe it (write it) to the hash object
    rs.pipe(hash);
  });
}

function getBufferSha1(data: Buffer): string {
  const hash = crypto.createHash('sha1');
  hash.update(data);
  return hash.digest('hex');
}



