import { newBsDmId } from '../utils';

import { AccessType } from '@brightsign/bscore';
import { BsDmId, BsDmIdNone } from '../bsDmInterfaces';

import { BsDmAction } from './actionBase';

// Actions
/** @internal */
/** @private */
export const ADD_USER_VARIABLE = 'DM_ADD_USER_VARIABLE';
/** @internal */
/** @private */
export const UPDATE_USER_VARIABLE = 'DM_UPDATE_USER_VARIABLE';
/** @internal */
/** @private */
export const DELETE_USER_VARIABLE = 'DM_DELETE_USER_VARIABLE';
/** @internal */
/** @private */
export const DELETE_USER_VARIABLES = 'DM_DELETE_USER_VARIABLES';

export interface UserVariableParams {
  id: BsDmId;
  name?: string;
  defaultValue?: string;
  access?: AccessType;
  isNetworked?: boolean;
  dataFeedId?: BsDmId;
  systemVariable?: string;    // TODO: use enum for SystemVariableType
}
export type UserVariableAction = BsDmAction<UserVariableParams>;

export function dmAddUserVariable(
  name: string,
  defaultValue: string,
  access: AccessType = AccessType.Private,
  isNetworked: boolean = false,
  dataFeedId: BsDmId = BsDmIdNone,
  systemVariable: string = '',
): UserVariableAction {
  return {
    type: ADD_USER_VARIABLE,
    payload: {
      id: newBsDmId(),
      name,
      defaultValue,
      access,
      isNetworked,
      dataFeedId,
      systemVariable,
    },
  };
}

export function dmUpdateUserVariable(params: UserVariableParams): UserVariableAction {
  return {
    type: UPDATE_USER_VARIABLE,
    payload: params,
  };
}

export function dmDeleteUserVariable(id: BsDmId): UserVariableAction {
  return {
    type: DELETE_USER_VARIABLE,
    payload: {id},
  };
}

export interface UserVariablesDeleteParams {
  ids: BsDmId[];
}
export type UserVariablesDeleteAction = BsDmAction<UserVariablesDeleteParams>;

export function dmDeleteUserVariables(ids: BsDmId[]): UserVariablesDeleteAction {
  return {
    type: DELETE_USER_VARIABLES,
    payload: {ids},
  };
}



