import { BsAssetItem, BsAssetId } from '@brightsign/bscore';
import { DmIdParam, DmLocatorParam } from '../bsDmInterfaces';
import { DmMap } from './index';
import {
  AssetItemAction, AssetItemParams, PUT_ASSET_ITEM,
  AssetItemRemoveAction, AssetItemRemoveParams, REMOVE_ASSET_ITEMS,
} from '../actions/actionAssetMap';
import { SignAction, OpenSignAction, OpenSignParams, NEW_SIGN, OPEN_SIGN } from '../actions/actionSign';

export type DmAssetIdMap = DmMap<BsAssetItem> | {};

const assetsById = (
  state: DmAssetMapState = {},
  {type, payload}: (AssetItemAction | AssetItemRemoveAction | SignAction | OpenSignAction),
): DmAssetMapState | {} => {
  switch (type) {
    case NEW_SIGN:
      return {};
    case OPEN_SIGN: {
      const {newState} = payload as OpenSignParams;
      // if undefined in given sign, create empty object
      return newState.assetMap ? newState.assetMap : {};
    }
    case PUT_ASSET_ITEM: {
      const {id} = payload as AssetItemParams;
      let newItem;
      if (state[id]) {
        newItem = Object.assign({}, state[id], payload);
      } else {
        newItem = Object.assign({}, payload);
      }
      newItem.refCount = newItem.refCount ? newItem.refCount + 1 : 1;
      return Object.assign({}, state, {[id]: newItem});
    }
    case REMOVE_ASSET_ITEMS: {
      const {assetIds} = payload as AssetItemRemoveParams;
      const newState = Object.assign({}, state);
      assetIds.forEach((id) => {
        if (newState[id].refCount && newState[id].refCount > 1) {
          newState[id].refCount = newState[id].refCount - 1;
        } else {
          delete newState[id];
        }
      });
      return newState;
    }
  }
  return state;
};

export type DmAssetMapState = DmAssetIdMap;

const assetMapReducer = assetsById;

/** @internal */
/** @private */
export default assetMapReducer;

/** @internal */
/** @private */
export const isValidAssetMapState = (state: DmAssetMapState): boolean => {
  return typeof state === 'object';
};

// Selectors

/** @internal */
/** @private */
export const getAssetItemById = (state: DmAssetMapState, props: DmIdParam): BsAssetItem | null => {
  const asset = state[props.id];
  return asset ? Object.assign({}, asset) : null;
};

/** @internal */
/** @private */
export const getAssetIdByLocator = (state: DmAssetMapState, props: DmLocatorParam): BsAssetId | null => {
  let assetId = null;
  const locator = props.locator;
  Object.keys(state).some((id): boolean => {
    if (state[id].locator === locator) {
      assetId = id;
      return true;
    }
  });
  return assetId;
};

/** @internal */
/** @private */
export const getAssetItemByLocator = (state: DmAssetMapState, props: DmLocatorParam): BsAssetItem | null => {
  let asset: BsAssetItem = null;
  const locator = props.locator;
  Object.keys(state).some((id): boolean => {
    const item = state[id];
    if (item[id].locator === locator) {
      asset = item;
      return true;
    }
  });
  return asset;
};

/** @internal */
/** @private */
export const getAssetItemIdsForSign = (state: DmAssetMapState) => Object.keys(state);



