/** @module Actions:DataFeed */
/**
 * Created by jimsugg on 1/24/17.
 */

import { newBsDmId } from '../utils';

import { AccessType, DataFeedUsageType, BsnFeedProperties } from '@brightsign/bscore';
import { BsDmId, BsDmIdNone, DmParameterizedString } from '../bsDmInterfaces';
import { dmResolveDmParameterizedString } from '../bsDmParameterizedStrings';

import { BsDmAction } from './actionBase';

// Actions
/** @internal */
/** @private */
export const ADD_DATA_FEED = 'DM_ADD_DATA_FEED';
/** @internal */
/** @private */
export const ADD_BSN_DATA_FEED = 'DM_ADD_BSN_DATA_FEED';
/** @internal */
/** @private */
export const UPDATE_DATA_FEED = 'DM_UPDATE_DATA_FEED';
/** @internal */
/** @private */
export const UPDATE_BSN_DATA_FEED = 'DM_UPDATE_BSN_DATA_FEED';
/** @internal */
/** @private */
export const DELETE_DATA_FEED = 'DM_DELETE_DATA_FEED';

export interface DataFeedParams {
  id: BsDmId;
  name?: string;
  url?: DmParameterizedString | string;
  usage?: DataFeedUsageType;
  updateInterval?: number;
  useHeadRequest?: boolean;
  parserPlugin?: BsDmId;
  autoGenerateUserVariables?: boolean;
  userVariableAccess?: AccessType;
}
export type DataFeedAction = BsDmAction<DataFeedParams>;

/**
 * Add a standard DataFeed, as specified by the feed URL
 *
 * @param name {string} - local (friendly) name for the data feed
 * @param url {string | DmParameterizedString} - URL for the data feed
 * @param usage {DataFeedUsageType} - One of (DataFeedUsageType.) Text, Mrss, MrssWith4K, Content -
 *  this parameter specifies how the feed will be used.
 * @param [updateInterval=300] {interval} - interval in seconds at which data feed should be checked.
 * @param [useHeadRequest=true] {boolean} - true if data feed checking should be optimized by sending HEAD requests,
 * and then updating only if Last-Modified-Date has changed.
 * @param [parserPlugin=BsDmIdNone] {BsDmId} - id of a parser plugin for the feed, or BsDmIdNone if no plugin is used.
 * @param [autoGenerateUserVariables=false] {boolean} - true to indicate that data feed consists of name/value pairs
 *  that should be converted to UserVariable values.
 * @param [userVariableAccess=Private] {AccessType} - One of (AccessType.) Private.
 *  If autoGenerateUserVariables is true, this parameter indicates the access level to be set for the user variables.
 * @returns {DataFeedAction}
 */
export function dmAddDataFeed(
  name: string,
  url: DmParameterizedString | string,
  usage: DataFeedUsageType,
  updateInterval: number = 300,
  useHeadRequest: boolean = true,
  parserPlugin: BsDmId = BsDmIdNone,
  autoGenerateUserVariables: boolean = false,
  userVariableAccess: AccessType = AccessType.Private,
): DataFeedAction {
  return {
    type: ADD_DATA_FEED,
    payload: {
      id: newBsDmId(),
      name,
      url: dmResolveDmParameterizedString(url),
      usage,
      updateInterval,
      useHeadRequest,
      parserPlugin,
      autoGenerateUserVariables,
      userVariableAccess,
    },
  };
}

export interface BsnDataFeedParams {
  id: BsDmId;
  name?: string;
  bsnDataFeed?: BsnFeedProperties;
  usage?: DataFeedUsageType;
  updateInterval?: number;
  useHeadRequest?: boolean;
  parserPlugin?: BsDmId;
  autoGenerateUserVariables?: boolean;
  userVariableAccess?: AccessType;
  supportsAudio?: boolean;
  matchPlayerTags?: boolean;
}
export type BsnDataFeedAction = BsDmAction<BsnDataFeedParams>;

/**
 * Update the properties for standard (URL based) data feed
 * @param params {DataFeedParams} - object containing properties to update. All properties are optional except for the
 *  'id' property, which must specify the data feed to be updated. The values of all other properties specified
 *  will replace the existing value of the corresponding property in the data feed object. Unspecified properties
 *  are left unchanged.
 *
 *  The type property cannot be changed. Attempts to do so will be ignored.
 * @returns {DataFeedAction}
 */
export function dmUpdateDataFeed(params: DataFeedParams): DataFeedAction {
  let localParams = params;
  if (params.hasOwnProperty('url')) {
    localParams = Object.assign({}, params, {url: dmResolveDmParameterizedString(params.url)});
  }
  return {
    type: UPDATE_DATA_FEED,
    payload: localParams,
  };
}

/**
 * Add a BSN DataFeed. BSN Data Feeds are those with any of the following DataFeedTypes:
 *  DataFeedType.BSNDataFeed, DataFeedType.BSNMediaFeed, DataFeedType.BSNDynamicPlaylist, DataFeedType.BSNTaggedPlaylist
 *
 * @param bsnDataFeed {BSNDataFeed} - BSNDataFeed object
 * @param usage {DataFeedUsageType} - One of (DataFeedUsageType.) Text, Mrss, MrssWith4K, Content -
 *  this parameter specifies how the feed will be used.
 * @param [name=BSN feed name] {string} - local (friendly) name for the data feed
 * @param [updateInterval=300] {interval} - interval in seconds at which data feed should be checked.
 * @param [useHeadRequest=true] {boolean} - true if data feed checking should be optimized by sending HEAD requests,
 * and then updating only if Last-Modified-Date has changed.
 * @param [parserPlugin=BsDmIdNone] {BsDmId} - id of a parser plugin for the feed, or BsDmIdNone if no plugin is used.
 * @param [autoGenerateUserVariables=false] {boolean} - true to indicate that data feed consists of name/value pairs
 *  that should be converted to UserVariable values.
 * @param [userVariableAccess=Private] {AccessType} - One of (AccessType.) Private, Shared
 *  If autoGenerateUserVariables is true, this parameter indicates the access level to be set for the user variables.
 * @param [supportsAudio=false] {boolean} - true if feed player needs to support audio.
 * @param [matchPlayerTags=false] {boolean} - true to match player tags within the feed.
 * @returns {BsnDataFeedAction}
 */
export function dmAddBsnDataFeed(
  bsnDataFeed: BsnFeedProperties,
  usage: DataFeedUsageType,
  name: string = '',
  updateInterval: number = 300,
  useHeadRequest: boolean = true,
  parserPlugin: BsDmId = BsDmIdNone,
  autoGenerateUserVariables: boolean = false,
  userVariableAccess: AccessType = AccessType.Private,
  supportsAudio: boolean = false,
  matchPlayerTags: boolean = false,
): BsnDataFeedAction {
  return {
    type: ADD_BSN_DATA_FEED,
    payload: {
      id: newBsDmId(),
      name: name ? name : bsnDataFeed.name,
      bsnDataFeed,
      usage,
      updateInterval,
      useHeadRequest,
      parserPlugin,
      autoGenerateUserVariables,
      userVariableAccess,
      supportsAudio,
      matchPlayerTags,
    },
  };
}

/**
 * Update the properties for a BSN data feed
 * @param params {BsnDataFeedParams} - object containing properties to update. All properties are optional except for
 *  the 'id' property, which must specify the data feed to be updated. The values of all other properties specified
 *  will replace the existing value of the corresponding property in the data feed object. Unspecified properties
 *  are left unchanged.
 *
 *  The type property cannot be changed. Attempts to do so will be ignored.
 * @returns {BsnDataFeedAction}
 */
export function dmUpdateBsnDataFeed(params: BsnDataFeedParams): BsnDataFeedAction {
  return {
    type: UPDATE_BSN_DATA_FEED,
    payload: params,
  };
}

/**
 * Delete a data feed object from the data feed collection.
 * There is currently no check to determine if the data feed object is used in the sign. If the object is referenced
 *  in a DataFeedContentItem or a MrssDataFeedContentItem, such references will become invalid if the data feed object
 *  is deleted.
 *
 * @param id {BsDmId} - id of the data feed object to delete
 * @returns {DataFeedAction}
 */
export function dmDeleteDataFeed(id: BsDmId): DataFeedAction {
  return {
    type: DELETE_DATA_FEED,
    payload: {id},
  };
}



